import Divider from '@/components/Divider';
import React, { useCallback, useEffect, useState } from 'react';
import styled from 'styled-components';
import api from '@/api/api';
import useInput from '@/hooks/useInput';

interface UserInfo {
  profileUrl: string;
  nickname: string;
  email: string;
  phone: string;
}

const ProfileManagement = () => {
  const [userData, setUserData] = useState<UserInfo | undefined>(undefined);
  const [nickname, setNickname] = useInput('');
  const [email, setEmail] = useInput('');
  const [phone, setPhone] = useInput('');

  const replaceAsterisk = (str: string) => {
    if (str.includes('@')) {
      const email = str.split('@');
      return `${email[0].slice(0, 3)}***@${email[1]}`;
    } else {
      return `${str.slice(0, 3)}-****-${str.slice(-4)}`;
    }
  };

  const onUpload = async (e: any) => {
    console.log(e.target.files[0]);
    if (e.target.files[0]) {
      await postProfileEdit(e.target.files[0]);
    }
  };

  const onDeleteProfileImg = async () => {
    await postProfileEdit();
  };

  const fetchUserData = async () => {
    await api
      .get('/mp/profile', {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      })
      .then((res) => {
        setUserData(res.data.data);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const postProfileEdit = async (image?: File) => {
    const formData = new FormData();
    if (userData === undefined) return;
    if (image !== undefined) {
      console.log(image);
      formData.append('imgUrl', image);
    }
    formData.append('nickName', nickname || userData.nickname);
    formData.append('subEmail', email || userData.email);
    formData.append('phone', phone || userData.phone);
    await api.patch('/mp/profile', formData).then((res) => {
      if (res.status === 200) {
        window.alert('프로필이 수정되었습니다.');
        fetchUserData();
      }
    });
  };

  useEffect(() => {
    fetchUserData();
  }, []);

  const defaultProfileImg =
    'https://raw.githubusercontent.com/MinJaeSon/assets/50f920a045a083d2c0a6676c32259ace88e9372e/default_profile.svg';

  return (
    userData && (
      <Wrapper>
        <h3>프로필 관리</h3>
        <Divider backgroundColor="#8993a2" />
        <ProfileSection>
          <img src={userData?.profileUrl || defaultProfileImg} />
          <InfoContainer>
            <h4>{userData?.nickname}</h4>
            <p>{userData?.email}</p>
            <InfoActionButtons>
              <div>
                <label htmlFor="fileInput">이미지 변경</label>
                <input id="fileInput" type="file" onChange={onUpload} />
              </div>
              <button onClick={onDeleteProfileImg}>삭제</button>
            </InfoActionButtons>
          </InfoContainer>
        </ProfileSection>
        <Divider />
        <EditContainer>
          <EditItem>
            <label>닉네임</label>
            <h4>{userData?.nickname}</h4>
            <input type="text" onChange={setNickname} />
            <p>
              변경 후 30일이 지나야 다시 변경 가능하므로 신중히 변경해주세요.
            </p>
            <EditActionButtons>
              <button>취소</button>
              <button
                onClick={() => {
                  postProfileEdit();
                }}
              >
                저장
              </button>
            </EditActionButtons>
          </EditItem>
          <EditItem>
            <label>이메일</label>
            <h4>{replaceAsterisk(userData.email)}</h4>
            <input type="text" onChange={setEmail} />
            <EditActionButtons>
              <button>취소</button>
              <button
                onClick={() => {
                  postProfileEdit();
                }}
              >
                저장
              </button>
            </EditActionButtons>
          </EditItem>
          <EditItem>
            <label>전화번호</label>
            <h4>{replaceAsterisk(userData.phone)}</h4>
            <input type="text" onChange={setPhone} />
            <EditActionButtons>
              <button>취소</button>
              <button
                onClick={() => {
                  postProfileEdit();
                }}
              >
                저장
              </button>
            </EditActionButtons>
          </EditItem>
        </EditContainer>
      </Wrapper>
    )
  );
};

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 1000px;
  h3 {
    text-align: left;
  }
`;

const ProfileSection = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  gap: 20px;
  border-radius: 10px;
  padding: 24px;
  img {
    width: 100px;
    height: 100px;
  }
`;

const InfoContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: start;
  h4 {
    font-size: 1rem;
    margin-top: 12px;
  }
  p {
    font-size: 0.8rem;
    color: #909090;
  }
  button {
    width: fit-content;
    height: 30px;
    margin-top: 12px;
    background-color: white;
    border: 1px solid #d5d5d5;
    border-radius: 10px;
    padding: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.8rem;
    color: #9fa19f;
    &:hover {
      cursor: pointer;
    }
  }
`;

const InfoActionButtons = styled.div`
  display: flex;
  flex-direction: row;
  gap: 12px;
  div {
    width: fit-content;
    height: 30px;
    margin-top: 12px;
    background-color: white;
    border: 1px solid #d5d5d5;
    border-radius: 10px;
    padding: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.8rem;
    color: #9fa19f;
    &:hover {
      cursor: pointer;
    }
  }
  input {
    display: none;
  }
`;

const EditContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  padding-right: 20px;
`;

const EditItem = styled.div`
  width: 612px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  gap: 5px;
  h4 {
    color: #222;
    font-size: 1rem;
    font-weight: 400;
    width: fit-content;
    margin-bottom: 8px;
  }
  label {
    width: fit-content;
    color: #a6a7a6;
  }
  span {
    font-size: 0.9rem;
    color: #78828d;
  }
  input {
    font-size: 1rem;
    border: none;
    border-bottom: 1px solid #222;
    &:focus {
      outline: none;
    }
  }
  p {
    margin-top: 4px;
    font-size: 0.8rem;
    width: fit-content;
    color: #a6a7a6;
  }
`;

const EditActionButtons = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  gap: 12px;
  margin-top: 24px;
  button {
    width: 120px;
    height: 52px;
    border: 1px solid #d5d5d5;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    &:hover {
      cursor: pointer;
    }
  }
  button:first-child {
    background-color: white;
    color: #222;
  }
  button:last-child {
    background-color: #000000;
    color: white;
  }
`;

export default ProfileManagement;
