import useInput from '@/hooks/useInput';
import axios from 'axios';
import React, { useCallback, useEffect, useState } from 'react';
import styled from 'styled-components';

type ButtonProps = {
  disabled: boolean;
};

type DropDownProps = {
  dropdown: string;
};

const SignUp = () => {
  const [email, onChangeEmail] = useInput('');
  const [phone, onChangePhone] = useInput('');
  const [nickname, onChangeNickname] = useInput('');
  const [carrier, setCarrier] = useState('');
  const [dropDown, setDropDown] = useState(false);
  const [unwrittenError, setUnwrittenError] = useState(true);

  const carriers = ['SKT', 'KT', 'LG'];

  const onSubmit = useCallback(
    async (e: React.FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      if (!email || !phone || !nickname) {
        setUnwrittenError(true);
        console.log('unwritten');
        return;
      }
      setUnwrittenError(false);
      try {
        await axios
          .post(
            process.env.REACT_APP_API_URL + '/api/auth/member-info',
            {
              nickName: nickname,
              phone,
              subEmail: email,
            },
            {
              headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${localStorage.getItem('token')}`,
              },
            },
          )
          .then((res) => {
            console.log(res);
            if (res.status === 200) {
              alert('회원가입이 완료되었습니다. 다시 로그인해주세요.');
              localStorage.removeItem('token');
              window.location.href = '/';
            }
          });
      } catch (err) {
        console.error(err);
      }
    },
    [email, phone, nickname],
  );

  useEffect(() => {
    if (!email || !phone || !nickname) {
      setUnwrittenError(true);
    } else {
      setUnwrittenError(false);
    }
  }, [unwrittenError, email, phone, nickname]);

  return (
    <Container disabled={unwrittenError}>
      <h1>회원가입</h1>
      <form onSubmit={onSubmit}>
        <label htmlFor="email">이메일</label>
        <input
          type="email"
          id="email"
          value={email}
          onChange={onChangeEmail}
          placeholder="이메일 입력"
        />
        <label htmlFor="phone">휴대폰 번호</label>
        <input
          type="phone"
          id="phone"
          value={phone}
          onChange={onChangePhone}
          placeholder="- 없이 숫자만 입력"
        />
        <label>통신사</label>
        <div>
          <input
            id="carrier"
            readOnly
            value={carrier}
            placeholder="선택해 주세요"
          />
          <img
            onClick={() => {
              setDropDown(!dropDown);
            }}
            src="https://velog.velcdn.com/images/ea_st_ring/post/3f778f75-c9bf-4727-b1f9-974e763d0986/image.svg"
          />
        </div>
        <DropDown dropdown={`${dropDown}`}>
          {carriers.map((carrier) => (
            <div key={carrier}>
              <input
                id={carrier}
                readOnly
                value={carrier}
                onClick={() => {
                  setCarrier(carrier);
                  setDropDown(false);
                }}
              />
            </div>
          ))}
        </DropDown>
        <label style={{ marginTop: '52px' }}>닉네임</label>
        <input
          id="nickname"
          value={nickname}
          onChange={onChangeNickname}
          placeholder="닉네임 입력"
        />
        <button type="submit" disabled={unwrittenError}>
          가입하기
        </button>
      </form>
    </Container>
  );
};

const Container = styled.div<ButtonProps>`
  display: flex;
  flex-direction: column;
  min-width: 500px;
  height: 100vh;
  justify-content: flex-start;
  align-items: center;
  box-sizing: border-box;
  form {
    display: flex;
    flex-direction: column;
    width: 500px;
    height: 100%;
    justify-content: flex-start;
  }
  h1 {
    color: #000;
    font-size: 32px;
    margin-bottom: 60px;
    align-self: center;
  }
  label {
    color: #222;
    font-size: 18px;
    font-weight: 500;
    width: 100%;
    text-align: left;
  }
  input {
    outline: none;
    border: none;
    border-bottom: 1px solid #ebebeb;
    width: 100%;
    height: 50px;
    font-weight: 400;
    font-size: 18px;
  }

  label + input {
    margin-top: 8px;
  }
  input + label {
    margin-top: 52px;
  }
  div {
    display: flex;
    justify-content: space-between;
  }
  div > img {
    position: relative;
    right: 2px;
    border-bottom: 1px solid #ebebeb;
    cursor: pointer;
  }

  button {
    width: 100%;
    height: 56px;
    border-radius: 10px;
    font-size: 20px;
    font-weight: 600;
    color: #fff;
    background-color: ${(props) => (props.disabled ? '#ebebeb' : '#999')};
    border: none;
    margin-top: 60px;
    cursor: pointer;
  }
`;

const DropDown = styled.div<DropDownProps>`
  position: relative;
  border-width: 0px 1px 1px 1px;
  border-style: solid;
  border-color: #ebebeb;
  top: 0px;
  width: 498px;
  height: 150px;
  background-color: #fff;
  z-index: 1;
  display: ${(props) =>
    props.dropdown == 'true' ? 'flex !important' : 'none !important'};
  flex-direction: column;
  div {
    width: 100%;
    height: 50px;
  }
  input:hover {
    background-color: #ebebeb;
  }
  div > input {
    width: 100%;
    height: 100%;
    font-size: 18px;
    font-weight: 400;
    text-align: start;
    padding-left: 20px;
    cursor: pointer;
  }
`;

export default SignUp;
