import Timer from '@/components/Timer';
import React, { useEffect } from 'react';
import styled from 'styled-components';

type TSelected = '3%' | '25%' | 'free';

type BidPhaseProps = {
  data: any;
  end: string;
  price: number;
  setPrice: React.Dispatch<React.SetStateAction<number>>;
  selected: string;
  setSelected: React.Dispatch<React.SetStateAction<TSelected>>;
  setPhase: React.Dispatch<React.SetStateAction<string>>;
};

const BidPhase = ({
  data,
  end,
  price,
  setPrice,
  selected,
  setSelected,
  setPhase,
}: BidPhaseProps) => {
  useEffect(() => {
    if (price === 0) {
      setSelected('3%');
      setPrice((data.high as number) * 1.03);
    }
  }, []);
  const checkPrice = () => {
    if (price < (data.high as number) * 1.03) {
      window.alert('최소 입찰가보다 낮습니다.');
      return false;
    }
    if (price > (data.high as number) * 1.25) {
      window.alert('최대 입찰가보다 높습니다.');
      return false;
    }
    return true;
  };

  return (
    <>
      <h2>
        원하시는 입찰가를 <br /> 선택해주세요.
      </h2>
      <InfoDiv>
        <img src={data.imageUrl} />
        <Info>
          <Name>{data.name}</Name>
          <Time>
            남은 시간 &nbsp;
            <Timer end={end} />
          </Time>
          <Price>
            현재가 &nbsp;
            <p>{new Intl.NumberFormat().format(data.high as number)}원</p>
          </Price>
        </Info>
      </InfoDiv>
      <Divider />
      <InputDiv>
        <NoticeDiv>
          <img src="https://velog.velcdn.com/images/ea_st_ring/post/e847f495-a45d-4ee6-ac2b-5d02c17e49a9/image.svg" />
          <p>
            입찰 경쟁 과열을 방지하기 위해 25%이상 증가된 금액 입력을
            제한합니다.
          </p>
        </NoticeDiv>
        <BidDiv
          style={{
            border:
              selected === '3%' ? '1px solid #ff4c4c' : '1px solid #d1d1d1',
          }}
          onClick={() => {
            setSelected('3%');
            setPrice((data.high as number) * 1.03);
          }}
        >
          <div>
            <p>최소입찰가</p>
            &nbsp; &nbsp;
            <span>
              {new Intl.NumberFormat().format((data.high as number) * 1.03)}원
            </span>
          </div>
          <div>최소입찰가는 현재가에 ‘현재가의 3%’를 더한값입니다.</div>
        </BidDiv>
        <BidDiv
          style={{
            border:
              selected === '25%' ? '1px solid #ff4c4c' : '1px solid #d1d1d1',
          }}
          onClick={() => {
            setSelected('25%');
            setPrice((data.high as number) * 1.25);
          }}
        >
          <div>
            <p>최대 입찰가</p>
            &nbsp; &nbsp;
            <span>
              {new Intl.NumberFormat().format((data.high as number) * 1.25)}원
            </span>
          </div>
          <div>최대입찰가는 현재가에 ‘현재가의 25%’를 더한값입니다.</div>
        </BidDiv>
        <BidDiv
          style={{
            border:
              selected === 'free' ? '1px solid #ff4c4c' : '1px solid #d1d1d1',
          }}
          onClick={() => {
            setSelected('free');
          }}
        >
          <p>직접 입력하기</p>
          <InputBox>
            <input
              type="text"
              placeholder="가격을 입력해주세요"
              value={price === 0 ? '' : new Intl.NumberFormat().format(price)}
              onChange={(e) => {
                setSelected('free');
                e.target.value = e.target.value
                  .replace(/[^0-9]/g, '')
                  .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');
                const value = e.target.value.replace(/,/g, '');
                setPrice(Number(value));
              }}
            />
            <p>원</p>
          </InputBox>
        </BidDiv>
        <button
          onClick={() => {
            if (checkPrice()) {
              setPhase('pay');
            }
          }}
        >
          구매 입찰 계속
        </button>
      </InputDiv>
    </>
  );
};

const InfoDiv = styled.div`
  width: 100%;
  display: flex;
  padding: 40px 20px;
  img {
    width: 100px;
    height: 100px;
  }
`;

const Info = styled.div`
  width: 100%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  margin-left: 20px;
`;

const Name = styled.div`
  font-size: 20px;
  width: 350px;
  margin-bottom: 8px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
`;

const Time = styled.div`
  font-size: 16px;
  margin-bottom: 28px;
  display: flex;
  color: #a1a1a1;
`;

const Price = styled.div`
  font-size: 16px;
  display: flex;
  color: #666666;
  p {
    color: #ff4c4c;
  }
`;

const Divider = styled.div`
  width: 100%;
  height: 12px;
  background-color: #f7f7f7;
`;

const InputDiv = styled.div`
  width: 100%;
  padding: 20px 20px;
  button {
    background-color: #222222;
    width: 100%;
    height: 50px;
    border: none;
    border-radius: 5px;
    color: #fff;
    margin-top: 20px;
    cursor: pointer;
    font-weight: 700;
  }
`;

const NoticeDiv = styled.div`
  width: 100%;
  height: 50px;
  background-color: #f9f9f9;
  color: #909090;
  display: flex;
  padding: 0 10px;
  align-items: center;
  border-radius: 5px;
  margin-bottom: 20px;
  img {
    width: 14px;
    height: 14px;
  }
  p {
    margin-left: 4px;
    font-size: 12px;
    line-height: 1.4;
  }
`;

const BidDiv = styled.div`
  width: 100%;
  height: 120px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 20px 20px;
  border: 1px solid #d1d1d1;
  border-radius: 5px;
  color: #5a5a5a;
  cursor: pointer;
  & + & {
    margin-top: 20px;
  }
  div {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    align-items: center;
    span {
      color: #222;
      font-size: 16px;
      font-weight: 600;
    }
  }
  div + div {
    margin-top: 12px;
  }

  input {
    width: 100%;
    height: 50px;
    border: none;
    border-bottom: 1px solid #d1d1d1;
  }
`;

const InputBox = styled.div`
  width: 100%;
  display: flex;
  align-items: flex-end !important;
  input {
    width: 100%;
    height: fit-content;
    padding: 0 8px;
    border: none;
    border-bottom: 1px solid #d1d1d1;
    margin-top: 24px;
    font-size: 16px;
    text-align: end;
  }
  input:focus {
    outline: none;
    border-bottom: 1px solid #ff4c4c;
    color: #ff4c4c;
  }
  input::placeholder {
    font-size: 16px;
    color: #cecece;
  }
`;

export default BidPhase;
