import api from '@/api/api';
import React, { useCallback, useState } from 'react';
import styled from 'styled-components';
import SearchAddress from './SearchAddress';
import DaumPostcodeEmbed from 'react-daum-postcode';
import { queryClient } from '@/App';

interface AddDeliveryProps {
  openAddModal: boolean;
  setOpenAddModal: React.Dispatch<React.SetStateAction<boolean>>;
}

const AddDelivery: React.FC<AddDeliveryProps> = ({ setOpenAddModal }) => {
  const [place, setPlace] = useState('');
  const [name, setName] = useState('');
  const [phone, setPhone] = useState('');
  const [address, setAddress] = useState('');
  const [zipcode, setZipcode] = useState('');
  const [detail, setDetail] = useState('');
  const [checked, setChecked] = useState(false);
  const [openPostcodeMoal, setOpenPostcodeModal] = useState(false);

  const onChangePlace = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      setPlace(e.target.value);
    },
    [place],
  );

  const onChangeName = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      setName(e.target.value);
    },
    [name],
  );

  const onChangePhone = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      setPhone(e.target.value);
    },
    [phone],
  );

  const onChangeDetail = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      setDetail(e.target.value);
    },
    [detail],
  );

  const registerAddress = () => {
    api
      .post(
        '/address',
        {
          addrName: place,
          isPrimary: checked,
          name: name,
          tel: phone,
          addr: address,
          zipNum: zipcode,
          addrDetail: detail,
        },
        {
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${localStorage.getItem('token')}`,
          },
        },
      )
      .then(() => {
        queryClient.invalidateQueries({ queryKey: ['apiGetAddress'] });
        setOpenAddModal(false);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const onSubmit = useCallback(
    (e: React.FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      console.log(place, name, phone, address, zipcode, detail, checked);
      if (place && name && phone && address && zipcode && detail) {
        registerAddress();
      } else {
        alert('입력값을 확인해주세요.');
      }
    },
    [place, name, phone, address, zipcode, detail, checked],
  );

  return (
    <Wrapper>
      <Background onClick={() => setOpenAddModal(false)}></Background>
      <Modal onSubmit={onSubmit}>
        {openPostcodeMoal ? (
          <SearchAddress
            setOpenPostcodeModal={setOpenPostcodeModal}
            setAddress={setAddress}
            setZipcode={setZipcode}
          />
        ) : (
          <Box>
            <Top>
              <img
                src="https://raw.githubusercontent.com/MinJaeSon/assets/01ca6913aa7ffcb6742f4ecea1bcaec3a0d19ed4/arrow_left.svg"
                onClick={() => setOpenAddModal(false)}
              />
              <h4>배송지 추가</h4>
            </Top>
            <Container>
              <InputSection>
                <input
                  type="text"
                  name="place"
                  placeholder="배송지명(최대 10글자)"
                  value={place}
                  onChange={onChangePlace}
                />
                <input
                  type="text"
                  name="name"
                  placeholder="이름 입력"
                  value={name}
                  onChange={onChangeName}
                />
                <input
                  type="text"
                  name="phone"
                  placeholder="휴대폰 번호(- 없이 숫자만 입력"
                  value={phone}
                  onChange={onChangePhone}
                />
                <input
                  type="text"
                  name="address"
                  placeholder="주소 검색"
                  value={address}
                  onClick={() => setOpenPostcodeModal(true)}
                />
                <input
                  type="text"
                  name="detail"
                  placeholder="상세 주소(예시 : 101동 101호)"
                  value={detail}
                  onChange={onChangeDetail}
                />
              </InputSection>
              <Checkbox>
                {checked ? (
                  <img
                    src="https://raw.githubusercontent.com/MinJaeSon/assets/193647f97d4b89eac97211097a0b095db96ac0b7/checkbox_filled%205.svg"
                    onClick={() => setChecked(!checked)}
                  />
                ) : (
                  <img
                    src="https://raw.githubusercontent.com/MinJaeSon/assets/193647f97d4b89eac97211097a0b095db96ac0b7/checkbox.svg"
                    onClick={() => setChecked(!checked)}
                  />
                )}
                <p>대표 배송지로 설정</p>
              </Checkbox>
            </Container>
          </Box>
        )}
        <Bottom>
          <button type="submit">완료</button>
        </Bottom>
      </Modal>
    </Wrapper>
  );
};

export default AddDelivery;

const Wrapper = styled.div`
  width: 100vw;
  height: 100vh;
  padding: 0px;
`;

const Background = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
`;

const Modal = styled.form`
  width: 548px;
  height: 100vh;
  padding: 0px;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 2;
`;

const Box = styled.div`
  display: flex;
  flex-direction: column;
`;

const Top = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 44px;
  margin-top: 32px;
  h4 {
    font-size: 1.1rem;
  }
  img {
    width: 20px;
    height: 20px;
    margin-right: 188px;
    margin-left: 24px;
    cursor: pointer;
  }
`;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  input {
    width: 500px;
    height: 40px;
    border: 1px solid #d1d1d1;
    border-radius: 5px;
    padding: 0 10px;
    margin-bottom: 20px;
    outline: none;
    &::placeholder {
      color: #cecece;
    }
  }
`;

const InputSection = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Checkbox = styled.div`
  display: flex;
  margin-left: 24px;
  margin-top: 4px;
  img {
    width: 20px;
    height: 20px;
    margin-right: 6px;
  }
  p {
    font-size: 0.8rem;
    color: #737373;
  }
`;

const Bottom = styled.div`
  border-top: 1px solid #ebebeb;
  margin-bottom: 0;
  button {
    width: 500px;
    height: 48px;
    margin: 16px 24px;
    border-radius: 5px;
    background: #222222;
    border: none;
    color: #fff;
    font-weight: 600;
    font-size: 0.85rem;
    cursor: pointer;
  }
`;
