import React from 'react';
import styled from 'styled-components';

const Footer = () => {
  return (
    <Wrapper>
      <div style={{ height: '1px', backgroundColor: '#e5e5e5' }}></div>
      <p>고객센터 1588 - 1111</p>
      <span>
        운영시간 평일 11:00 - 18:00 (주말, 공휴일 휴무)
        <br />
        점심시간 평일 13:00 - 14:00
      </span>
    </Wrapper>
  );
};

export default Footer;

const Wrapper = styled.div`
  padding: 0px 220px 120px 220px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  p {
    color: #000;
    text-align: center;
    font-family: Noto Sans;
    font-size: 0.8rem;
    font-weight: 400;
    margin-top: 40px;
  }
  span {
    color: #6a6a6a;
    text-align: center;
    font-family: Noto Sans;
    font-size: 0.7rem;
    font-weight: 400;
  }
`;
