import React from 'react';
import styled from 'styled-components';
import Timer from './Timer';
import { useNavigate } from 'react-router-dom';

interface AucItemProps {
  id?: number;
  bid?: number;
  img?: string;
  name?: string;
  price?: number;
  end?: string;
}


const AucItem: React.FC<AucItemProps> = ({ id, bid, img, name, price, end }) => {

  const remainTime = end?.split('T')[1];
  let hour: number = 0;
  let min: number = 0;
  let sec: number = 0;
  if (typeof remainTime === 'string') {
    hour = parseInt((remainTime as string).split(':')[0]);
    min = parseInt((remainTime as string).split(':')[1]);
    sec = parseInt((remainTime as string).split(':')[2]);
  }

  const navigate = useNavigate();

  return (
    <Container
      onClick={() => {
        if (!id) {
          window.alert('잘못된 접근입니다.');
          return;
        }
        navigate(`/detail/${id}`, {
          state: {
            img: img,
          },
        });
      }}
    >
      <ImageWrapper>
        <img src={img} />
        <p>입찰 {bid}</p>
      </ImageWrapper>
      <InfoWrapper>
        <h3>{name}</h3>
        <div>경매</div>
        <p>
          <span>{price?.toLocaleString('ko-KR')}원</span>
        </p>
        <Timer end={end as string} />
      </InfoWrapper>
    </Container>
  );
};

export default AucItem;

const Container = styled.div`
  width: 200px;
  height: fit-content;
  display: flex;
  flex-direction: column;
`;

const ImageWrapper = styled.div`
  width: 200px;
  height: 192px;
  border-radius: 10px;
  background: #f4f4f4;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    width: 160px;
    height: 160px;
    width: 100%;
    height: 100%;
    border-radius: 10px;
  }
  p {
    position: absolute;
    top: 8px;
    right: 8px;
    color: #4a4d4a;
    font-family: Noto Sans KR;
    font-size: 0.7rem;
    font-weight: 400;
    margin: 0;
  }
`;

const InfoWrapper = styled.div`
  display: flex;
  flex-direction: column;
  text-align: left;
  justify-content: flex-start;
  margin-top: 12px;
  h3 {
    color: #4a4a4a;
    font-family: Noto Sans;
    font-size: 0.9rem;
    font-weight: 400;
    margin-top: 0;
    margin-bottom: 6px;
    min-height: 38px;
  }
  p {
    color: #4a4a4a;
    font-family: Noto Sans;
    font-size: 0.9rem;
    font-weight: 400;
    margin-top: 0;
    margin-bottom: 6px;
  }
  div {
    width: 28px;
    height: 22px;
    border-radius: 4px;
    background: #ffe5e5;
    color: #ff6d6d;
    text-align: center;
    font-family: Noto Sans KR;
    font-size: 0.7rem;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 12px;
  }
  span {
    font-size: 1rem;
    font-weight: 600;
  }

  h4 {
    color: #9fa19f;
    font-family: Noto Sans;
    font-size: 0.8rem;
    font-weight: 400;
    margin-top: 4px;
    margin-bottom: 0;
  }
`;
