import Divider from '@/components/Divider';
import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import Sidebar from './Sidebar';
import Main from './Main';
import api from '@/api/api';
import useSWR from 'swr';
import fetcher from '@/utils/fetcher';

// 카테고리를 정하는 사이드바 구현
// 상태 관리를 index.tsx에서

const Shop = () => {
  const [category, setCategory] = useState('');
  const [datas, setDatas] = useState([]); // [aucs, sale, aucs, sale]
  // 경매상품 포함, 일반상품 포함, 판매완료 상품 포함 순서
  const [includes, setIncludes] = useState([true, true, false]);
  const [price, setPrice] = useState('');
  const [orderBy, setOrderBy] = useState('최신순');
  const [page, setPage] = useState(0);
  const url = [
    ['/aucs/hand/list', '/aucs/norm/list'],
    ['/sale/hand/list', '/sale/norm/list'],
    '/all/list' + `?page=${page}&size=20`,
  ];
  const { data, error } = useSWR(
    '/all/list' + `?page=${page}&size=20`,
    fetcher,
    {
      onSuccess: (data: any) => {
        console.log(data.data.content);
        setDatas(data.data.content);
      },
      onError: (error: any) => {
        console.error(error);
      },
    },
  );
  const token = localStorage.getItem('token');
  if (!token) {
    alert('로그인이 필요한 서비스입니다.');
    window.location.replace('/');
  }

  useEffect(() => {
    if (includes[0] && includes[1] && data) {
      setDatas(data.data.content);
    } else if (includes[0] && includes[1] && !data) {
      return;
    } else {
      includes.forEach((include, index) => {
        if (include) {
          const fetch1 = api.get(url[index][0]);
          const fetch2 = api.get(url[index][1]);
          Promise.all([fetch1, fetch2]).then((res) => {
            const allData = res.reduce((acc, cur) => {
              return acc.concat(cur.data.data);
            }, []);
            console.log(allData);
            setDatas(allData);
          });
        }
      });
    }
  }, [includes]);

  if (!data) {
    return <div>로딩...</div>;
  }

  return (
    <Container>
      <Title>SHOP</Title>
      <Divider />
      <Content>
        <Sidebar
          includes={includes}
          setIncludes={setIncludes}
          category={category}
          setCategory={setCategory}
          price={price}
          setPrice={setPrice}
        />
        <Main
          data={datas}
          category={category}
          orderBy={orderBy}
          setOrderBy={setOrderBy}
        />
      </Content>
    </Container>
  );
};

const Container = styled.div`
  width: 100%;
  height: 100%;
  padding: 0px;
`;

export const Title = styled.div`
  font-size: 1.6rem;
  font-weight: 500;
  margin-top: 16px;
  margin-bottom: 20px;
  text-align: center;
`;

const Content = styled.div`
  display: flex;
  flex-direction: row;
  padding: 40px 160px;
`;

export default Shop;
