import Divider from '@/components/Divider';
import React from 'react';
import { useLocation, useParams } from 'react-router-dom';
import styled from 'styled-components';

const Complete = () => {
  const category = useParams<{ category: string }>().category;
  return (
    <Container>
      <Title>SELL</Title>
      <Divider />
      <Content>
        <Info>
          SELL &nbsp;{'>'}&nbsp;
          {category === 'sale' ? '일반판매' : '경매판매'} &nbsp;{'>'}&nbsp;{' '}
          등록완료
        </Info>
        <h2>등록완료</h2>
        <Divider
          marginTop="32px"
          marginBottom="48px"
          backgroundColor="#8993A2"
        />
        <div style={{ alignItems: 'center', justifyContent: 'center' }}>
          <img src="https://velog.velcdn.com/images/ea_st_ring/post/b02beeed-aecf-4780-8341-833f0d11925e/image.svg" />
          <h1 className="complete_text">상품 등록이 완료되었습니다.</h1>
        </div>
        <Divider marginTop="48px" marginBottom="32px" />
        <Box>
          <img src="https://velog.velcdn.com/images/ea_st_ring/post/770ff624-b8fa-4f48-a72c-d447da63c9bd/image.png" />
        </Box>
      </Content>
    </Container>
  );
};

const Container = styled.div`
  width: 100%;
  height: 100%;
  padding: 0px;
`;

const Title = styled.div`
  font-size: 32px;
  font-weight: normal;
  margin-bottom: 20px;
`;

const Info = styled.div`
  width: 100%;
  font-size: 16px;
  font-weight: normal;
  margin-bottom: 40px;
  display: flex;
  align-self: start;
  color: #a1a1a1;
`;

const Content = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 160px;
  h2 {
    align-self: start;
  }
  div {
    display: flex;
  }

  .complete_text {
    margin-bottom: 0;
    align-self: center;
  }
`;

const Box = styled.div`
  width: 100%;
  height: 250px;
  border: 1px solid #d1d1d1;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 24px;
  img {
    max-width: 100%;
    max-height: 100%;
    border: 1px solid #d1d1d1;
    border-radius: 15px;
  }
`;

export default Complete;
