import api from '@/api/api';
import Divider from '@/components/Divider';
import useInput from '@/hooks/useInput';
import styled from '@emotion/styled';
import {
  Box,
  Button,
  Container,
  Input,
  Modal,
  Stack,
  Typography,
} from '@mui/material';
import React from 'react';

//
//
//

interface AskModalProps {
  imgUrl: string;
  name: string;
  productId: number | string | undefined;
  price: number;
  open: boolean;
  onClose: () => void;
}

//
//
//

const AskModal = ({
  imgUrl,
  productId,
  name,
  price,
  open,
  onClose,
}: AskModalProps) => {
  const [title, setTitle] = useInput('');
  const [description, setDescription] = useInput('');

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 700,
    height: 740,
    bgcolor: 'background.paper',
    boxShadow: 24,
    p: 4,
  };

  const handleSubmit = async () => {
    if (!title || !description) {
      alert('제목과 내용을 입력해주세요.');
      return;
    }

    await api.post(`/detail/${productId}/board`, {
      title,
      content: description,
    });
    onClose();
  };

  return (
    <Modal open={open} onClose={onClose}>
      <Box width={400} sx={style}>
        <Box>
          <Button
            onClick={onClose}
            sx={{ position: 'absolute', right: '0.2rem', top: '0.5rem' }}
          >
            <Typography variant="body1" color="#000">
              X
            </Typography>
          </Button>
        </Box>
        <Typography variant="h5" marginBottom="1.5rem">
          문의하기
        </Typography>
        <Stack direction="row" spacing={2}>
          <img src={imgUrl} alt={name} width={100} />
          <Stack height={100} padding="0.25rem">
            <Typography variant="body1">{name}</Typography>
            <Box
              display="flex"
              alignItems="flex-end"
              justifySelf="flex-end"
              height="stretch"
            >
              <Typography
                variant="subtitle1"
                color="#A1A1A1"
                marginRight="1rem"
              >
                가격
              </Typography>
              <Typography variant="subtitle1">
                {price.toLocaleString()}원
              </Typography>
            </Box>
          </Stack>
        </Stack>
        <Divider marginTop="2rem" />
        <Stack
          display="flex"
          direction="row"
          alignItems="center"
          width="100%"
          padding="1.5rem"
        >
          <Typography variant="subtitle1" width="4rem">
            제목
          </Typography>
          <Input
            onChange={setTitle}
            value={title}
            sx={{
              border: '1px solid #d1d1d1',
              outline: 'none',
              borderRadius: '5px',
              width: '400px',
              height: '2.5rem',
            }}
            placeholder="제목을 입력해주세요."
          />
          <Box width="8rem" display="flex" justifyContent="center">
            <input type="checkbox" />
            <Typography
              variant="subtitle1"
              marginLeft="0.5rem"
              marginTop="0.1rem"
              height="100%"
              textAlign="center"
            >
              비밀글
            </Typography>
          </Box>
        </Stack>
        <Divider />
        <Stack
          display="flex"
          direction="row"
          alignItems="flex-start"
          width="100%"
          padding="1.5rem"
        >
          <Typography variant="subtitle1" width="4rem">
            설명
          </Typography>
          <Input
            multiline
            rows={10}
            sx={{
              border: '1px solid #d1d1d1',
              outline: 'none',
              borderRadius: '5px',
              width: '400px',
            }}
            placeholder="문의하실 내용을 입력해주세요."
            onChange={setDescription}
          />
        </Stack>
        <Divider marginTop="1.5rem" />
        <Stack
          display="flex"
          direction="row"
          width="100%"
          justifyContent="center"
          gap="1.5rem"
          marginTop="1.5rem"
        >
          <Button
            variant="outlined"
            sx={{ border: '1px solid #bbbbbb', color: '#222' }}
            onClick={onClose}
          >
            <Typography variant="subtitle1">취소</Typography>
          </Button>
          <Button
            variant="contained"
            sx={{ backgroundColor: '#000' }}
            onClick={handleSubmit}
          >
            <Typography variant="subtitle1">작성하기</Typography>
          </Button>
        </Stack>
      </Box>
    </Modal>
  );
};

export default AskModal;
