/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useCallback, useRef, useState } from 'react';
import styled from 'styled-components';
import Slider from 'react-slick';
import { CustomArrowProps } from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import Slide1 from './Slide1';
import prevBtn from '@/assets/banner_left_arrow.svg';
import nextBtn from '@/assets/banner_right_arrow.svg';

const Banner = () => {
  const [currentSlide, setCurrentSlide] = useState(0);

  const slickRef = useRef<Slider | null>(null);

  const prevArrow = useCallback(() => slickRef.current?.slickPrev(), []);
  const nextArrow = useCallback(() => slickRef.current?.slickNext(), []);

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    autoplay: true,
    autoplaySpeed: 4000,
    cssEase: 'linear',
    slidesToShow: 1,
    slidesToScroll: 1,
    pauseOnHover: true,
    beforeChange: (current: number, next: number) => setCurrentSlide(next),
    appendDots: (dots: any) => (
      <div style={{ background: 'transparent' }}>
        <ul>{dots}</ul>
      </div>
    ),
    customPaging: (i: number) => (
      <div
        style={{
          width: '40px',
          height: '2px',
          background: i === currentSlide ? '#d9d9d9' : '#d9d9d98b',
        }}
      ></div>
    ),
  };

  return (
    <Wrapper>
      <PrevBtn src={prevBtn} onClick={prevArrow} />
      <Slider {...settings} ref={slickRef}>
        <Slide1 />
        <Slide1 />
        <Slide1 />
        <Slide1 />
      </Slider>
      <NextBtn src={nextBtn} onClick={nextArrow} />
    </Wrapper>
  );
};

export default Banner;

const Wrapper = styled.div`
  width: 100%;
  height: 420px;
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  .slick-dots {
    bottom: 20px;
    li {
      width: 40px;
      margin: 4px;
    }
  }
`;

const PrevBtn = styled.img`
  width: 20px;
  position: absolute;
  left: 80px;
  top: 45%;
  z-index: 1;
`;

const NextBtn = styled.img`
  width: 20px;
  position: absolute;
  right: 80px;
  top: 45%;
  z-index: 1;
`;
