import React, { useEffect } from 'react';
import { DataGrid, GridColDef } from '@mui/x-data-grid';
import { useQuery } from '@tanstack/react-query';
import api from '@/api/api';
import { useLocation, useParams } from 'react-router-dom';
import styled from '@emotion/styled';
import {
  Pagination,
  Paper,
  Stack,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableFooter,
  TableHead,
  TableRow,
} from '@mui/material';

//
//
//

const ROWS_PER_PAGE = 5;

//
//
//

const Board = () => {
  const productsId = useLocation().pathname.split('/')[2];
  const [page, setPage] = React.useState(1);
  const [rows, setRows] = React.useState<any>([]);
  const ref = React.useRef<HTMLDivElement>(null);

  const { data } = useQuery({
    queryKey: ['getDetailProductsIdBoard', productsId],
    queryFn: async () => {
      return api
        .get(`/detail/${productsId}/board`)
        .then((res) => res.data.data);
    },
  });

  const pages = Math.ceil(data?.length / ROWS_PER_PAGE);

  const handleChange = (event: React.ChangeEvent<unknown>, value: number) => {
    setPage(value);
    if (ref?.current) {
      ref?.current.scrollIntoView({ behavior: 'smooth' });
    }
  };

  const columns: GridColDef[] = [
    { field: 'number', headerName: '번호', width: 90, align: 'center' },
    { field: 'isAnswered', headerName: '답변여부', width: 90, align: 'center' },
    { field: 'title', headerName: '', width: 130, align: 'left' },
    { field: 'content', headerName: '내용', width: 130, align: 'left' },
    { field: 'email', headerName: '작성자', width: 130, align: 'center' },
  ];

  useEffect(() => {
    if (data) {
      setRows(data.slice((page - 1) * ROWS_PER_PAGE, page * ROWS_PER_PAGE));
    }
  }, [data, page]);

  const renderTable = () => {
    return (
      <Stack height={400} overflow="auto" justifyContent="space-between">
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                {columns.map((column) => (
                  <TableCell key={column.field} align={column.align}>
                    {column.headerName}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {rows?.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={columns.length} align="center">
                    작성된 게시글이 없습니다.
                  </TableCell>
                </TableRow>
              ) : (
                rows?.map((row: any) => (
                  <TableRow key={row.posts_id}>
                    <TableCell
                      component="th"
                      scope="row"
                      align="center"
                      width={100}
                    >
                      {row.posts_id}
                    </TableCell>
                    <TableCell align="center" width={100}>
                      {row.isAnswered ? '답변완료' : '답변대기'}
                    </TableCell>
                    <TableCell align="left" width={200}>
                      {row.title}
                    </TableCell>
                    <TableCell align="left" width={400}>
                      {row.content}
                    </TableCell>
                    <TableCell align="center" width={100}>
                      {row.email}
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </TableContainer>
        <Stack
          width="100%"
          justifyContent="center"
          alignItems="center"
          marginTop="2rem"
        >
          <Pagination
            ref={ref}
            count={pages}
            page={page}
            onChange={handleChange}
            shape="rounded"
            size="small"
          />
        </Stack>
      </Stack>
    );
  };

  return <div>{renderTable()}</div>;
};

//
//
//
export default Board;
