import Divider from '@/components/Divider';
import React from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import * as shop from '@/pages/Shop/index';

const Sell = () => {
  const token = localStorage.getItem('token');
  const navigate = useNavigate();
  if (!token) {
    alert('로그인이 필요한 서비스입니다.');
    window.location.replace('/');
  }

  return (
    <Container>
      <shop.Title>SELL</shop.Title>
      <Divider />
      <Content>
        <div>
          <Box
            style={{
              backgroundImage: `url(https://velog.velcdn.com/images/ea_st_ring/post/0f2d71ed-a841-4376-882b-0a65563e10c4/image.svg)`,
            }}
            onClick={() => {
              navigate('/sell/auc');
            }}
          >
            <h2>경매판매 {'>'}</h2>
            <p>경매판매로 중고거래의 최고 수익을 창출해보세요!</p>
          </Box>
          <Box
            style={{
              backgroundImage: `url(https://velog.velcdn.com/images/ea_st_ring/post/5a3ebd51-5560-40ad-b566-f8a3aabdedaa/image.svg)`,
            }}
            onClick={() => {
              navigate('/sell/sale');
            }}
          >
            <h2>일반판매 {'>'}</h2>
            <p>안전하고 빠른 일반판매로 중고상품을 거래할 수 있어요!</p>
          </Box>
        </div>
        <img src="https://velog.velcdn.com/images/ea_st_ring/post/83b1fbae-cf24-49af-8005-e539b38014b4/image.svg" />
        <CautionBox>
          <h3>주의사항</h3>
          <li>
            신뢰할 수 있는 거래를 위해 상품 설명 및 사진을 상세히 적어주세요.
          </li>
          <li>
            허위 거래 및 사기 등의 행위는 이용 제한 및 법적 제재의 대상이 될 수
            있습니다.
          </li>
          <li>주의사항... 더 생각해보면 좋을 것 같아요</li>
        </CautionBox>
      </Content>
      <Divider marginBottom="20px" />
    </Container>
  );
};

const Container = styled.div`
  width: 100%;
  height: 100%;
  padding: 0px;
`;

const Content = styled.div`
  display: flex;
  flex-direction: column;
  padding: 40px 160px;
  div {
    display: flex;
    height: 200px;
    color: #fff;
    margin-bottom: 40px;
  }
  div + div {
    margin-left: 40px;
  }
`;

const Box = styled.div`
  width: 100%;
  background-image: url('https://velog.velcdn.com/images/ea_st_ring/post/0f2d71ed-a841-4376-882b-0a65563e10c4/image.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 36px;
  h2 {
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 12px;
  }
  p {
    color: #d0d0d0;
    font-family: Noto Sans KR;
    font-size: 16px;
    width: 270px;
    text-align: left;
  }
`;

const CautionBox = styled.div`
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: 10px;
  border: 1px solid #e6e6e6;
  margin-top: 40px;
  color: #555 !important;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 40px;
  h3 {
    margin-bottom: 24px;
    font-size: 20px;
  }
  li {
    font-size: 16px;
    margin-bottom: 8px;
  }
`;

export default Sell;
