import React from 'react';
import DaumPostcodeEmbed from 'react-daum-postcode';
import styled from 'styled-components';

interface SearchAddressProps {
  setOpenPostcodeModal: React.Dispatch<React.SetStateAction<boolean>>;
  setAddress: React.Dispatch<React.SetStateAction<string>>;
  setZipcode: React.Dispatch<React.SetStateAction<string>>;
}

const SearchAddress: React.FC<SearchAddressProps> = ({ setOpenPostcodeModal, setAddress, setZipcode }) => {
  const handleComplete = (data: any) => {
    console.log(data);
    // console.log(data.address);
    setOpenPostcodeModal(false);
    setAddress(data.address.replace(',', ' ') + ' (' + data.zonecode + ')');
    setZipcode(data.zonecode);
  }

  const style = {
    width: '100%',
    height: '100%',
  }

  return (
    <Wrapper>
      <Background onClick={() => setOpenPostcodeModal(false)}></Background>
      <Modal>
      <DaumPostcodeEmbed onComplete={handleComplete} style={style} />
      </Modal>
    </Wrapper>
  );
};

export default SearchAddress;

const Wrapper = styled.div`
  width: 100vw;
  height: 100vh;
  padding: 0px;
`;

const Background = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  /* background: rgba(0, 0, 0, 0.5); */
  z-index: 1;
`;

const Modal = styled.div`
  width: 548px;
  height: 100%;
  padding: 0px;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 2;
`;