import React from 'react';
import { useNavigate } from 'react-router';
import styled from 'styled-components';

interface NorItemProps {
  id?: number;
  img?: string;
  name?: string;
  price?: number;
}

const NorItem: React.FC<NorItemProps> = ({ id, img, name, price }) => {
  const navigate = useNavigate();

  return (
    <Container
      onClick={() => {
        if (!id) {
          window.alert('잘못된 접근입니다.');
          return;
        }
        navigate(`/detail/${id}`, {
          state: {
            img: img,
          },
        });
      }}
    >
      <ImageWrapper>
        <img src={img} />
      </ImageWrapper>
      <InfoWrapper>
        <p>{name}</p>
        <div>일반</div>
        <p>
          <span>{price?.toLocaleString('ko-KR')}원</span>
        </p>
      </InfoWrapper>
    </Container>
  );
};

export default NorItem;

const Container = styled.div`
  width: 200px;
  height: fit-content;
  display: flex;
  flex-direction: column;
`;

const ImageWrapper = styled.div`
  width: 200px;
  height: 192px;
  border-radius: 10px;
  background: #f4f4f4;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    /* width: 160px;
    height: 160px; */
    width: 100%;
    height: 100%;
    border-radius: 10px;
  }
`;

const InfoWrapper = styled.div`
  display: flex;
  flex-direction: column;
  text-align: left;
  justify-content: flex-start;
  margin-top: 12px;
  p {
    color: #4a4a4a;
    font-family: Noto Sans;
    font-size: 0.9rem;
    font-weight: 400;
    margin-top: 0;
    margin-bottom: 6px;
    min-height: 38px;
  }
  div {
    width: 28px;
    height: 22px;
    border-radius: 4px;
    background: #f4f4f4;
    color: #8993a2;
    text-align: center;
    font-family: Noto Sans KR;
    font-size: 0.7rem;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 12px;
  }
  span {
    font-size: 1rem;
    font-weight: 600;
  }
`;
