import React, { useState } from 'react';
import styled from 'styled-components';
import Postcode from '@/components/PostPopUp';
import useInput from '@/hooks/useInput';
import api from '@/api/api';

const ManageAddress = ({ setShowAddress }: { setShowAddress: any }) => {
  const [showAddAddress, setShowAddAddress] = useState(false);
  const [addressName, setAddressName] = useInput('');
  const [isPriamry, setIsPrimary] = useState(false);
  const [name, setName] = useInput('');
  const [phone, setPhone] = useInput('');
  const [address, setAddress] = useState('');
  const [zipNum, setZipNum] = useState('' as string);
  const [detailAddress, setDetailAddress] = useInput('');

  const postAddress = async () => {
    api
      .post('/address', {
        addrName : addressName,
        isPriamry,
        name,
        tel: phone,
        addr: address,
        zipNum,
        addrDetail: detailAddress,
      })
      .then((res) => {
        console.log(res);
      });
  };

  return (
    <Wrapper>
      <TopBar>
        <img
          src="https://velog.velcdn.com/images/ea_st_ring/post/fa66daa9-ddeb-4ca5-894c-5fa5f38a0340/image.svg"
          onClick={() => {
            setShowAddress(false);
          }}
        />
        <h2>{showAddAddress ? '배송지 추가' : '배송지 관리'}</h2>
      </TopBar>
      {!showAddAddress ? (
        <Content>
          <span>등록된 배송지 정보가 없습니다.</span>
          <p>배송지는 최대 5개까지 등록할 수 있습니다.</p>
          <button
            onClick={() => {
              setShowAddAddress(true);
            }}
          >
            배송지 추가
          </button>
        </Content>
      ) : (
        <AddAddress>
          <input
            onChange={setAddressName}
            type="text"
            placeholder="배송지명"
          ></input>
          <input onChange={setName} type="text" placeholder="이름 입력"></input>
          <input
            onChange={setPhone}
            type="text"
            placeholder="휴대폰 번호(- 없이 숫자만 입력)"
          ></input>
          <Postcode setAddress={setAddress} setZipNum={setZipNum} />
          <input
            onChange={setDetailAddress}
            type="text"
            placeholder="상세 주소(예시 : 101동 101호)"
          ></input>
          <div>
            <input
              type="checkbox"
              onChange={() => setIsPrimary(!isPriamry)}
            ></input>
            <p>대표 배송지로 설정</p>
          </div>
          <button
            onClick={() => {
              setShowAddAddress(false);
              postAddress();
            }}
          >
            완료
          </button>
        </AddAddress>
      )}
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 100%;
  height: fit-content;
  display: flex;
  flex-direction: column;
  padding: 20px;
`;

const TopBar = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  img {
    cursor: pointer;
    transform: rotate(90deg);
  }
  h2 {
    padding: 0;
    margin: auto;
  }
`;

const Content = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px 0;
  p {
    color: #a1a1a1;
    font-size: 14px;
    margin-top: 4px;
  }

  button {
    width: 100%;
    height: 50px;
    margin-top: 20px;
    border: 1px solid #d1d1d1;
    background-color: #fff;
    cursor: pointer;
    border-radius: 5px;
  }
`;

const AddAddress = styled.div`
  width: 100%;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0;
  input {
    width: 100%;
    height: 50px;
    margin-top: 20px;
    border: 1px solid #d1d1d1;
    background-color: #fff;
    border-radius: 5px;
    padding: 0 12px;
  }
  button {
    width: 100%;
    height: 50px;
    background-color: #000;
    border: none;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 100px;
  }
  div {
    width: 100%;
    display: flex;
    align-items: center;
    margin-left: 8px;
    input {
      width: 16px;
      margin: 0;
    }
    p {
      margin-left: 8px;
      color: #d7d7d7;
    }
  }
`;

export default ManageAddress;
