import React from 'react';
import styled from 'styled-components';
import banner1 from '@/assets/banner_1.svg';
import sneakers from '@/assets/banner_1_img.png';

const Slide1 = () => {
  return (
    <Wrapper>
      <img src={sneakers} alt='신발 이미지' />
      <p>중고 경매 플랫폼 Aucison</p>
      <span>Aucison이 처음이신가요?</span>
    </Wrapper>
  );
};

export default Slide1;

const Wrapper = styled.div`
  background: url(${banner1});
  background-size: cover;
  height: 420px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  img {
    width: 400px;
  }
  p {
    color: #cdcdcd;
    font-family: Noto Sans;
    font-size: 0.9rem;
    font-weight: 500;
    margin-top: 32px;
    margin-bottom: 4px;
  }
  span {
    color: #fff;
    text-align: center;
    font-family: Noto Sans;
    font-size: 1.5rem;
    font-weight: 600;
  }
`;
