import Login from '@/pages/Login';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const TopBar = () => {
  const [login, setLogin] = useState(false);

  const token = localStorage.getItem('token');

  useEffect(() => {
    if (token) {
      setLogin(true);
    }
  }, [token]);

  const [isOpen, setIsOpen] = useState(false);
  const handleOpen = () => {
    setIsOpen(true);
  };
  const handleClose = () => {
    setIsOpen(false);
  };
  const handleLogout = () => {
    localStorage.removeItem('token');
    window.location.replace('/');
  };
  return (
    <Container>
      {isOpen && (
        <Login handleOpen={handleOpen} handleClose={handleClose}></Login>
      )}
      <ExtraSection>
        <ExtraWrapper>
          <img
            src="https://raw.githubusercontent.com/MinJaeSon/assets/ef1f0c206ac16847a2bd7e586f44bfc9d416d411/info.svg"
            alt="info_icon"
          />
          <p>SHOP INFORMATION</p>
        </ExtraWrapper>
        <ExtraWrapper>
          <img
            src="https://raw.githubusercontent.com/MinJaeSon/assets/ef1f0c206ac16847a2bd7e586f44bfc9d416d411/star.svg"
            alt="start_icon"
            style={{ width: '17px', height: '17px', marginRight: '4px' }}
          />
          <p>즐겨찾기</p>
        </ExtraWrapper>
      </ExtraSection>
      <MenuSection>
        <MenuItem as={Link} to="/mypage">
          마이페이지
        </MenuItem>
        <MenuItem as={Link} to="/mypage/likes">
          관심
        </MenuItem>
        <MenuItem
          onClick={token ? handleLogout : handleOpen}
          style={{ cursor: 'pointer' }}
        >
          {token ? '로그아웃' : '로그인'}
        </MenuItem>
      </MenuSection>
    </Container>
  );
};

export default TopBar;

const Container = styled.div`
  width: 100%;
  height: 32px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 100px;
  box-sizing: border-box;
  border-bottom: 1px solid #eee;
`;

const ExtraSection = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

const ExtraWrapper = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  img {
    width: 13px;
    height: 13px;
    padding-top: 1px;
    margin-right: 6px;
  }
  p {
    color: #666;
    font-family: Noto Sans;
    font-size: 0.75rem;
    font-weight: 400;
  }
  &:first-child {
    margin-right: 32px;
  }
`;

const MenuSection = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`;

const MenuItem = styled.div`
  color: #666;
  font-family: Noto Sans;
  font-size: 0.75rem;
  font-weight: 400;
  text-decoration: none;
  &:not(:last-child) {
    margin-right: 30px;
  }
`;
