import React from 'react';

type DividerProps = {
  width?: string;
  marginTop?: string;
  marginBottom?: string;
  backgroundColor?: string;
};

/**
 * 자주 사용되는 구분선 컴포넌트
 * @param width: 구분선의 너비
 * @param marginTop: 구분선의 위쪽 여백
 * @param marginBottom: 구분선의 아래쪽 여백
 * @param backgroundColor: 구분선의 색상
 * @returns 
 */
const Divider = ({
  width = '100%',
  marginTop = '0px',
  marginBottom = '0px',
  backgroundColor = '#eaeaea',
}: DividerProps) => {
  return (
    <div
      style={{
        width: width,
        height: '1px',
        backgroundColor: backgroundColor,
        marginTop,
        marginBottom,
      }}
    ></div>
  );
};

export default Divider;
