import React, { useEffect, useState } from 'react';
import { Outlet, NavLink, useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { ReactComponent as LineCol } from '@assets/line.svg';
import { ReactComponent as LineRow } from '@assets/line_row.svg';
import { ReactComponent as AucIcon } from '@assets/auc_icon.svg';
import { ReactComponent as NorIcon } from '@assets/nor_icon.svg';
import Data from '@/dummy.json';
import AucItem from '@components/AucItem';
import api from '@/api/api';

interface UserInfo {
  profileUrl: string;
  nickname?: string;
  email?: string;
  phone?: string;
  credit: number;
}

interface PurchaseData {
  historiesId: number;
  productName: string;
  productImgUrl: string;
  category: string;
  kind: string;
  ordersId: number;
  createdTime: string;
  oStatus: string;
  price: number;
}

//
//
//

const DEFAULT_PROFILE_IMAGE_URL =
  'https://raw.githubusercontent.com/MinJaeSon/assets/50f920a045a083d2c0a6676c32259ace88e9372e/default_profile.svg';

//
//
//

const Default = () => {
  const [userData, setUserData] = useState<UserInfo | undefined>(undefined);
  const [purchaseData, setPurchaseData] = useState<PurchaseData[]>();
  const navigate = useNavigate();

  const fetchUserData = async () => {
    await api
      .get('/mp/profile', {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      })
      .then((res) => {
        setUserData(res.data.data);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  const fetchPurchaseData = async () => {
    await api
      .get('/mp/buy', {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      })
      .then((res) => {
        setPurchaseData(res.data.data);
      })
      .catch((err) => {
        console.error(err);
      });
  };

  useEffect(() => {
    fetchUserData();
    fetchPurchaseData();
  }, []);

  return (
    <Wrapper>
      <ProfileSection>
        <img
          src={
            userData?.profileUrl === null
              ? DEFAULT_PROFILE_IMAGE_URL
              : userData?.profileUrl
          }
        />
        <InfoContainer>
          <h4>{userData?.nickname}</h4>
          <p>{userData?.email}</p>
          <button onClick={() => navigate('/mypage/myprofile')}>
            프로필 관리
          </button>
        </InfoContainer>
        <StateContainer>
          <StateItem>
            <span>옥시즌페이</span>
            <p> {userData?.credit.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')}원</p>
          </StateItem>
          <LineCol style={{ height: '40px' }} />
          <StateItem>
            <span>구매내역</span>
            <p>0</p>
          </StateItem>
          <LineCol style={{ height: '40px' }} />
          <StateItem>
            <span>판매내역</span>
            <p>0</p>
          </StateItem>
          <LineCol style={{ height: '40px' }} />
          <StateItem>
            <span>관심상품</span>
            <p>0</p>
          </StateItem>
        </StateContainer>
      </ProfileSection>
      <PurchaseSection>
        <Top>
          <h4>구매 내역</h4>
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              alignItems: 'center',
            }}
            onClick={() => navigate('/mypage/purchase')}
          >
            <p>더보기</p>
            <img
              src="https://raw.githubusercontent.com/MinJaeSon/assets/f4275f00b1cc001bcbd0ff9875977ae5d54dbeb1/arrow_right.svg"
              style={{ width: '10px' }}
            />
          </div>
        </Top>
        <Contents>
          <Header>
            <p>주문일</p>
            <p style={{ marginLeft: '140px' }}>주문내역</p>
            <p style={{ marginLeft: '200px' }}>주문번호</p>
            <p>주문금액</p>
            <p>진행상태</p>
          </Header>
          <ContentList>
            <ContentItem>
              {/* <p style={{ marginRight: '-64px' }}>2024.02.07</p>
              <img src="https://github.com/MinJaeSon/assets/blob/main/auc_item.png?raw=true" />
              <AucIcon style={{ width: '28px', margin: '0 -80px' }} />
              <p>Thisisneverthat x New Era x MLB New York Yankees New</p>
              <p>01390292</p>
              <p>54,300원</p>
              <p>응찰중</p> */}
            </ContentItem>
          </ContentList>
        </Contents>
      </PurchaseSection>
      <SalesSection>
        <Top>
          <h4>판매 내역</h4>
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              alignItems: 'center',
            }}
            onClick={() => navigate('/mypage/sales')}
          >
            <p>더보기</p>
            <img
              src="https://raw.githubusercontent.com/MinJaeSon/assets/f4275f00b1cc001bcbd0ff9875977ae5d54dbeb1/arrow_right.svg"
              style={{ width: '10px' }}
            />
          </div>
        </Top>
        <Contents>
          <Header>
            <p>등록일</p>
            <p style={{ marginLeft: '140px' }}>등록내역</p>
            <p style={{ marginLeft: '200px' }}>등록번호</p>
            <p>등록금액</p>
            <p>진행상태</p>
          </Header>
          <ContentList>
            {/* <ContentItem>
              <p style={{ marginRight: '-64px' }}>2024.02.07</p>
              <img src="https://github.com/MinJaeSon/assets/blob/main/auc_item.png?raw=true" />
              <AucIcon style={{ width: '28px', margin: '0 -80px' }} />
              <p>Thisisneverthat x New Era x MLB New York Yankees New</p>
              <p>01390292</p>
              <p>54,300원</p>
              <p>판매중</p>
            </ContentItem> */}
          </ContentList>
        </Contents>
      </SalesSection>
      <LikesSection>
        <Top>
          <h4>관심상품</h4>
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              alignItems: 'center',
            }}
            onClick={() => navigate('/mypage/likes')}
          >
            <p>더보기</p>
            <img
              src="https://raw.githubusercontent.com/MinJaeSon/assets/f4275f00b1cc001bcbd0ff9875977ae5d54dbeb1/arrow_right.svg"
              style={{ width: '10px' }}
            />
          </div>
        </Top>
        <LikeList>
          {/* {Data.items.slice(0, 4).map((item) => (
            <AucItem
              key={item.id}
              bid={item.bid}
              img={item.img}
              name={item.name}
              price={item.price}
              end={item.remain}
            />
          ))} */}
        </LikeList>
      </LikesSection>
    </Wrapper>
  );
};

export default Default;

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 48px;
  width: 1000px;
`;

const ProfileSection = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 20px;
  border: 1px solid #ebebeb;
  border-radius: 10px;
  padding: 24px;
  img {
    width: 100px;
    height: 100px;
  }
`;

const InfoContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: start;
  margin-left: -320px;
  h4 {
    font-size: 1rem;
    margin-top: 12px;
  }
  p {
    font-size: 0.8rem;
    color: #909090;
  }
  button {
    width: 100px;
    height: 30px;
    margin-top: 12px;
    background-color: white;
    border: 1px solid #d5d5d5;
    border-radius: 10px;
    padding: 18px 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.8rem;
    color: #9fa19f;
    &:hover {
      cursor: pointer;
    }
  }
`;

const StateContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding-right: 20px;
`;

const StateItem = styled.div`
  display: flex;
  flex-direction: column;
  gap: 5px;
  span {
    font-size: 0.9rem;
    color: #78828d;
  }
  p {
    font-size: 1rem;
    color: #222222;
  }
`;

const Top = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  h4 {
    font-size: 1.1rem;
  }
  p {
    font-size: 0.8rem;
    color: #6a6a6a;
    margin-right: 4px;
    &:hover {
      cursor: pointer;
    }
  }
`;

const Contents = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const Header = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 32px;
  gap: 20px;
  background-color: #f9f9f9;
  width: 100%;
  height: 44px;
  border-radius: 10px;
  p {
    color: #6e706e;
    font-size: 0.9rem;
  }
`;

const ContentList = styled.div`
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 0 40px 0 24px;
`;

const ContentItem = styled.div`
  display: flex;
  flex-direction: row;
  gap: 20px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  img {
    width: 50px;
    height: 50px;
  }
  p {
    font-size: 0.8rem;
    color: #727272;
  }
  p:nth-child(2) {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 40px;
  }
`;

const PurchaseSection = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
`;

const SalesSection = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
`;

const LikesSection = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
`;

const LikeList = styled.div`
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 100%;
`;
