import React, { useState } from 'react';
import styled from 'styled-components';

type TagProps = {
  tags: string[] | null;
  setTags: React.Dispatch<React.SetStateAction<string[] | null>>;
};

const Tag = ({ tags, setTags }: TagProps) => {
  const removeTags = (indexToRemove: number) => {
    const filter = tags?.filter((el, index) => index !== indexToRemove);
    if (filter) setTags(filter);
  };

  const addTags = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (tags) {
      if (tags.length >= 5) {
        alert('태그는 최대 5개까지 입력 가능합니다.');
        return;
      }
      const target = event.target as HTMLInputElement;
      const inputVal = target.value;
      if (tags?.includes(inputVal)) {
        alert('이미 추가된 태그입니다.');
        return;
      }
      setTags([...tags, inputVal]);
      target.value = '';
    }
  };

  return (
    <>
      <TagsInput>
        <Tags>
          {tags?.map((tag, index) => (
            <li key={index} className="tag">
              <span className="tag-title">{tag}</span>
              <img
                src="https://velog.velcdn.com/images/ea_st_ring/post/d5f4409f-39c9-43bd-bb35-c6d4fa8774be/image.svg"
                className="delete-icon"
                onClick={() => removeTags(index)}
              ></img>
            </li>
          ))}
        </Tags>
        <input
          type="text"
          onKeyUp={(e) => {
            {
              if (e.key === 'Enter' && e.currentTarget.value !== '') {
                addTags(e);
              }
            }
          }}
          placeholder="태그 입력 후 엔터를 입력해주세요. (최대 5개)"
        />
      </TagsInput>
    </>
  );
};

const TagsInput = styled.div`
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 700px;
  padding: 0 8px;
  border: 1px solid #e6e6e6;
  border-radius: 6px;
  font-size: 16px;
  .tag {
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 8px;
    list-style: none;
    border-radius: 6px;
    margin: 0 8px 8px 0;
    background: rgb(34, 34, 34);
    border-radius: 15px;
  }
  .delete-icon {
    width: 18px;
    height: 18px;
    margin-left: 8px;
    cursor: pointer;
  }

  span {
    color: #ffffff;
    font-size: 16px;
  }

  input {
    flex: 1;
    border: none;
    height: 46px;
    font-size: 16px;
    padding: 0 8px;
    :focus {
      outline: transparent;
    }
  }
`;

const Tags = styled.ul`
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 8px 0 0 0;
`;

export default Tag;
