import api from '@/api/api';
import Timer from '@/components/Timer';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import ManageAddress from './ManageAddress';
import { useParams } from 'react-router-dom';

type TAddress = {
  addrName: string;
  addr: string;
  addrDetail: string;
  isPrimary: boolean;
  name: string;
  tel: string;
  zipNum: string;
}[];

const PayPhase = ({
  data,
  end,
  price,
}: {
  data: any;
  end: string;
  price: number;
}) => {
  const [addressData, setAddressData] = useState<TAddress | null>();
  const [showAddress, setShowAddress] = useState(false);
  const [infoData, setInfoData] = useState<any>();
  const productId = useParams().id;
  const fetchAddressData = async () => {
    await api
      .get('/address')
      .then((res) => {
        setAddressData(res.data.data);
        console.log(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const fetchInfoData = async () => {
    await api
      .get('/payment/' + productId, {
        params: {
          addrName: addressData ? addressData[0]?.addrName : null,
          bidAmount: Math.floor(price * 1.05),
        },
      })
      .then((res) => {
        console.log(res.data);
        setInfoData(res.data.data);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    fetchAddressData();
  }, []);

  useEffect(() => {
    fetchInfoData();
  }, [addressData]);

  return (
    <>
      {!showAddress ? (
        <>
          <h2>결제하기</h2>
          <InfoDiv>
            <img src={data.imageUrl} />
            <Info>
              <Name>{data.name}</Name>
              <Time>
                남은 시간 &nbsp;
                <Timer end={end} />
              </Time>
              <Price>
                나의 입찰희망가 &nbsp;
                <p>{new Intl.NumberFormat().format(price)}원</p>
              </Price>
            </Info>
          </InfoDiv>
          <Divider></Divider>
          <AddressDiv>
            {addressData ? (
              addressData?.map((address: any, idx: number) => (
                <div key={idx}>
                  <h3>{address.addrName}</h3>
                  <p>
                    {address.name} {address.tel}
                    <br />[{address.zipNum}] {address.addr}
                    <br />
                    {address.addrDetail}
                  </p>
                </div>
              ))
            ) : (
              <NoAddressDiv>
                <p>배송지를 추가해주세요.</p>
                <button
                  onClick={() => {
                    setShowAddress(true);
                  }}
                >
                  배송지 추가
                </button>
              </NoAddressDiv>
            )}
          </AddressDiv>
          <Divider></Divider>
          <PaymentDiv>
            <h3>결제 방법</h3>
            <Payments>
              <PayDiv>
                <div>A</div>&nbsp; pay
              </PayDiv>
              <button>카드결제</button>
              <button>무통장입금</button>
              <button>휴대폰결제</button>
            </Payments>
            <CreditDiv>
              <img src="https://velog.velcdn.com/images/ea_st_ring/post/e847f495-a45d-4ee6-ac2b-5d02c17e49a9/image.svg" />
              &nbsp;
              <p>사용 가능한 금액</p>
              &nbsp; &nbsp;
              <span>{new Intl.NumberFormat().format(infoData?.credit)}원</span>
            </CreditDiv>
          </PaymentDiv>
          <Divider />
          <ResultDiv>
            <div>
              <h4>최종 결제 금액</h4>
              <span>
                {new Intl.NumberFormat().format(Math.floor(price * 1.05))}원
              </span>
            </div>
            <div>
              <p>상품 금액</p>
              <p>{new Intl.NumberFormat().format(price)}원</p>
            </div>
            <div>
              <p>안전거래 수수료(5% 적용)</p>
              <p>
                {new Intl.NumberFormat().format(Math.floor(price * 0.05))}원
              </p>
            </div>
          </ResultDiv>
          <ResultInfo>
            입찰 실수 및 허위입찰은 경매사고로 이어질 수 있으며, Aucison에
            등록된 판매물품의 내용 및 판매진행은 판매자의 전적인 책임으로
            이어집니다. 거래 및 결제와 관련된 모든 책임은 판매자와 구매자에게
            있습니다.
          </ResultInfo>
          <PayButtonDiv>
            <button>
              {new Intl.NumberFormat().format(Math.floor(price * 1.05))}원
              결제하기
            </button>
          </PayButtonDiv>
        </>
      ) : (
        <ManageAddress setShowAddress={setShowAddress} />
      )}
    </>
  );
};

const InfoDiv = styled.div`
  width: 100%;
  display: flex;
  padding: 40px 20px;
  img {
    width: 100px;
    height: 100px;
  }
`;

const Info = styled.div`
  width: 100%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  margin-left: 20px;
`;

const Name = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 20px;
  width: 350px;
  margin-bottom: 8px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
`;

const Time = styled.div`
  font-size: 16px;
  margin-bottom: 28px;
  display: flex;
  color: #a1a1a1;
`;

const Price = styled.div`
  font-size: 16px;
  display: flex;
  color: #666666;
  p {
    color: #ff4c4c;
  }
`;

const Divider = styled.div`
  width: 100%;
  min-height: 12px;
  background-color: #f7f7f7;
  margin: 12px 0;
`;

const AddressDiv = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: start;
  padding: 40px 20px;
  line-height: 1.6;
  div {
    margin-top: 12px;
    display: flex;
    flex-direction: column;
    text-align: left;
  }
  p {
    color: #5a5a5a;
  }
  span {
    color: #ff6d6d;
    text-decoration: underline #ff6d6d;
  }
`;

const NoAddressDiv = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  p {
    margin: 12px 0px 24px 0;
    color: #a1a1a1;
  }
  button {
    width: 100%;
    height: 45px;
    border: 1px solid #d1d1d1;
    border-radius: 5px;
    background-color: #fff;
    cursor: pointer;
  }
`;

const PaymentDiv = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 40px 20px;
`;

const Payments = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 4px;
  margin-top: 20px;
  button {
    width: 235px;
    height: 40px;
    border: 1px solid #d1d1d1;
    background-color: #fff;
    border-radius: 5px;
  }
`;

const PayDiv = styled.div`
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ff4c4c;
  border-radius: 5px;
  color: #fff;
  div {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 16px;
    height: 16px;
    color: #ff4c4c;
    background-color: #fff;
    border-radius: 50%;
  }
`;

const CreditDiv = styled.div`
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 20px;
  background-color: #f9f9f9;
  margin-top: 12px;
  border-radius: 5px;
  p {
    font-size: 12px;
  }
`;

const ResultDiv = styled.div`
  width: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  div {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  div + div {
    margin-top: 12px;
  }
  div:first-child {
    margin-bottom: 20px;
  }
  span {
    color: #ff4c4c;
  }
`;

const ResultInfo = styled.div`
  background-color: #f9f9f9;
  font-size: 10px;
  padding: 20px;
  text-align: left;
`;

const PayButtonDiv = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  button {
    width: 95%;
    height: 50px;
    background-color: #ff4c4c;
    color: #fff;
    border: none;
    border-radius: 5px;
    margin-top: 20px;
    cursor: pointer;
    margin-bottom: 20px;
  }
`;

export default PayPhase;
