import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import HomeHotItemList from '@/components/HomeHotItemList';
import HomeRecentItemList from '@/components/HomeRecentItemList';
import Footer from '@/components/Footer';
import Banner from '@/components/Banner';
import api from '@/api/api';

interface Data {
  productId: number;
  name: string;
  kind: string;
  category: string;
  imgUrl: string;
  totCnt?: number;
  end?: string;
  high?: number;
  price?: number;
}

const Main = () => {
  const [hotProductData, setHotProductData] = useState<Data[]>();
  const [recentProductData, setRecentProductData] = useState<Data[]>();

  useEffect(() => {
    api
      .get('/home')
      .then((res) => {
        setHotProductData(res.data.data.popularAucsProducts);
        setRecentProductData(res.data.data.recentSaleProducts);
        console.log(hotProductData);
        console.log(recentProductData);
      })
      .catch((err) => {
        console.error(err);
      });
  }, []);

  return (
    <Wrapper>
      <Banner />
      {hotProductData && <HomeHotItemList product={hotProductData} />}
      <div
        style={{
          height: '1px',
          background: '#EBEBEB',
          width: 'calc(100% - 400px)',
        }}
      />
      {recentProductData && <HomeRecentItemList product={recentProductData} />}
      <Footer />
    </Wrapper>
  );
};

export default Main;

const Wrapper = styled.div`
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
`;
