import Divider from '@/components/Divider';
import React, { useRef, useState } from 'react';
import styled from 'styled-components';

type SidebarProps = {
  includes: boolean[];
  setIncludes: React.Dispatch<React.SetStateAction<boolean[]>>;
  category: string;
  setCategory: React.Dispatch<React.SetStateAction<string>>;
  price: string;
  setPrice: React.Dispatch<React.SetStateAction<string>>;
};

const Sidebar = ({
  includes,
  setIncludes,
  category,
  setCategory,
  price,
  setPrice,
}: SidebarProps) => {
  const includeTexts = ['경매상품 포함', '일반상품 포함', '판매완료 상품 포함'];
  const priceTexts = [
    '10만원 이하',
    '10만원 ~ 30만원 이하',
    '30만원 ~ 50만원 이하',
    '50만원 이상',
  ];
  const prices = [0, 10, 30, 50]
  const radioRefs = useRef<(HTMLInputElement | null)[]>([]);
  const [toggle, setToggle] = useState(false);

  

  return (
    <Container>
      <TitleBar>
        <Title>카테고리</Title>
        <ToggleButton
          src="https://velog.velcdn.com/images/ea_st_ring/post/3f778f75-c9bf-4727-b1f9-974e763d0986/image.svg"
          alt="arrow"
          onClick={() => {
            setToggle(!toggle);
          }}
          style={toggle ? { transform: 'rotateX(-180deg)' } : {}}
        />
      </TitleBar>
      {toggle && (
        <>
      <p
        onClick={() => {
          setCategory('일반');
        }}
        style={category === '일반' ? { color: 'black', fontWeight: '600' } : {}}
      >
        일반
      </p>
      <p
        onClick={() => {
          setCategory('핸드메이드');
        }}
        style={
          category === '핸드메이드' ? { color: 'black', fontWeight: '600' } : {}
        }
      >
        핸드메이드
      </p>
      <Divider marginTop="32px" />
      <TitleBar>
        <Title>필터</Title>
        <InitializeButton
          onClick={() => {
            setIncludes([false, false, false]);
          }}
        >
          초기화
        </InitializeButton>
      </TitleBar>
      {includes.map((include, index) => (
        <CheckBar key={index}>
          <img
            src={
              include
                ? 'https://velog.velcdn.com/images/ea_st_ring/post/2328fd24-cd5e-4182-b350-bb71c1e94e5e/image.svg'
                : 'https://velog.velcdn.com/images/ea_st_ring/post/6d744a5a-c418-4da6-9aee-49610cecc37a/image.svg'
            }
            onClick={() => {
              const newIncludes = [...includes];
              newIncludes[index] = !newIncludes[index];
              setIncludes(newIncludes);
            }}
            alt="check"
          />
          <label>{includeTexts[index]}</label>
        </CheckBar>
      ))}
      <Divider marginTop="32px" />
      <TitleBar>
        <Title>가격</Title>
        <InitializeButton
          onClick={() => {
            radioRefs.current.forEach((radioRef) => {
              if (radioRef) {
                radioRef.checked = false;
                setPrice('');
              }
            });
          }}
        >
          초기화
        </InitializeButton>
      </TitleBar>
      {priceTexts.map((priceText, index) => (
        <CheckBar key={index}>
          <input
            type="radio"
            id={prices[index].toString()}
            name="price"
            value={priceText}
            ref={(el) => {
              if (el) {
                radioRefs.current.push(el);
              }
            }}
            onClick={() => {
              setPrice(prices[index].toString());
            }}
            onChange={() => {}
            }
            checked={price === prices[index].toString()}
          />
          <label htmlFor={priceText}>{priceText}</label>
        </CheckBar>
      ))}
      <Hide/>
      </>
          )}
    </Container>
  );
};

const Container = styled.div`
  min-width: 185px;
  height: 100%;
  text-align: start;
  font-size: 20px;
  p {
    font-size: 16px;
    color: #737373;
    cursor: pointer;
  }
  p + p {
    margin-top: 18px;
  }
`;

const TitleBar = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  div + & {
    margin-top: 24px;
  }
`;

const Title = styled.h3`
  font-size: 20px;
`;

const ToggleButton = styled.img`
  width: 20px;
  height: 20px;
  margin-left: 20px;
  cursor: pointer;
  transition: 0.2s;
`;

const InitializeButton = styled.h4`
  color: #737373;
  font-size: 12px;
  text-decoration-line: underline;
  cursor: pointer;
`;

const CheckBar = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 12px;
  input {
    width: 20px;
    height: 20px;
  }
  input[type='radio'] {
    appearance: none;
    border: 1px solid #ebebeb;
    border-radius: 50%;
  }
  input[type='radio']:checked {
    border: 5px solid black;
  }

  label {
    color: #737373;
    font-size: 14px;
    margin-left: 8px;
  }
`;

const Hide = styled.div`
  z-index: 100;
  background: white;
  position: absolute;
  width: 185px;
  top: 290px;
  animation: slide-down 0.5s ease-in-out;
  @keyframes slide-down {
    0% {
      top: 290px;
      height: 570px;
    }
    100% {
      top: 820px;
      height: 0;
    }
  }
`;

export default Sidebar;
