import api from '@/api/api';
import Divider from '@/components/Divider';
import React, { useEffect, useState } from 'react';
import { useLocation, useParams, useSearchParams } from 'react-router-dom';
import styled from 'styled-components';
import Dummy from '@/dummy.json';
import Sale from './Sale';
import Aucs from './Aucs';
import Product from '@/db/typing';
import * as shop from '@/pages/Shop/index';

const index = () => {
  const { id } = useParams();
  const [data, setData] = useState();
  const location = useLocation();
  const img = location.state.img;
  const token = localStorage.getItem('token');
  if (!token) {
    alert('로그인이 필요한 서비스입니다.');
    window.location.replace('/');
  }

  const fetchDetail = async () => {
    await api
      .get(`/detail/${id}`, {
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
          'Content-Type': 'application/json',
          Accept: 'application/json',
        },
      })
      .then((res) => {
        res.data.data.imageUrl = img;
        const arr = res.data.data;
        setData(arr);
      });
  };

  useEffect(() => {
    fetchDetail();
  }, []);

  if (!data) return <div>loading...</div>;

  return (
    <Container>
      <shop.Title>SHOP</shop.Title>
      <Divider />
      {(data as Product).category === 'SALE' ? (
        <Sale data={data} id={id} />
      ) : (
        <Aucs data={data} id={id} />
      )}
    </Container>
  );
};

const Container = styled.div`
  width: 100%;
  height: 100%;
  padding: 0px;
`;

export default index;
