import React, { useEffect } from 'react';
import { useGoogleLogin } from '@react-oauth/google';
// import api from '@/api/api';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

interface Props {
  handleClose: () => void;
  handleOpen: () => void;
}

const Login = ({ handleClose, handleOpen }: Props) => {
  useEffect(() => {
    // 유저의 스크롤 막기
    document.body.style.overflow = 'hidden';
    return () => {
      document.body.style.overflow = 'unset';
    };
  }, []);

  const navigate = useNavigate();

  const handleLoginClick = async (credential: any) => {
    try {
      const res = await axios.get(
        process.env.REACT_APP_API_URL + '/api/auth/google/callback',
        {
          params: {
            accessToken: credential,
          },
          headers: {
            "Content-Type": "application/json",
          },
        },
      );
      localStorage.setItem('token', res.data.jwtToken);
      if (res.data.newUser) {
        navigate('/signup');
      } else {
        window.location.replace('/');
      }
    } catch (err) {
      console.error(err);
    }
  };

  const stopPropagation = (e: any) => {
    e.stopPropagation();
  };

  const LoginButton = useGoogleLogin({
    onSuccess: (res: any) => {
      console.log(res.access_token);
      handleLoginClick(res.access_token);
      handleClose();
    },
  });

  return (
    <Wrapper onClick={handleClose}>
      <React.Fragment>
        <Modal onClick={stopPropagation}>
          <CloseButton
            onClick={handleClose}
            src="https://velog.velcdn.com/images/ea_st_ring/post/d5f4409f-39c9-43bd-bb35-c6d4fa8774be/image.svg"
          />
          <img src="https://velog.velcdn.com/images/ea_st_ring/post/50d85d09-43c2-4c79-8194-1bc70f196a21/image.svg" />
          <h2>옥시젼으로 중고거래 시작하기</h2>
          <p>간편하게 가입하고 상품을 확인하세요</p>
          <Button onClick={() => LoginButton()}>
            <img src="https://velog.velcdn.com/images/ea_st_ring/post/7e480c6a-0b07-4e75-90b7-79db5da0c02f/image.svg" />
            <p>구글로 시작하기</p>
          </Button>
        </Modal>
      </React.Fragment>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 100%;
  min-height: 100dvh;
  background-color: #f7f7f7;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  background: rgba(0, 0, 0, 0.6);
`;

const Modal = styled.div`
  width: 562px;
  height: 565px;
  flex-shrink: 0;
  border-radius: 10px;
  border: 1px solid #e6e6e6;
  background: #f7f7f7;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0;
  img {
    width: 38px;
    height: 38px;
    margin-bottom: 12px;
  }
  h2 {
    font-size: 26px;
    margin-bottom: 20px;
  }
  p {
    font-size: 16px;
    margin-bottom: 40px;
  }
`;

const Button = styled.button`
  width: 360px;
  height: 56px;
  border-radius: 10px;
  background: #fff;
  color: #000;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
  font-size: 16px;
  font-weight: 500;
  border: none;
  outline: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  img {
    width: 21px;
    height: 21px;
    margin: 0 90px 0 16px;
  }
  p {
    margin: 0;
  }
`;

const CloseButton = styled.img`
  width: 24px;
  height: 24px;
  position: relative;
  align-self: flex-end;
  top: 0px;
  right: 32px;
  margin-bottom: 0;
  cursor: pointer;
`;

export default Login;
