import Timer from '@/components/Timer';
import React from 'react';
import styled from 'styled-components';
import BidPhase from './BidPhase';
import PayPhase from './PayPhase';
type AucsSideSectionProps = {
  data: any;
  isOpen: boolean;
  setIsOpen: React.Dispatch<React.SetStateAction<boolean>>;
  end: string;
};

type TSelected = '3%' | '25%' | 'free';

const AucsSideSection = ({
  data,
  isOpen,
  setIsOpen,
  end,
}: AucsSideSectionProps) => {
  const [price, setPrice] = React.useState(0);
  const [selected, setSelected] = React.useState<TSelected>('3%');
  const [phase, setPhase] = React.useState('bid');

  return (
    isOpen && (
      <Wrapper
        onClick={() => {
          setIsOpen(false);
        }}
      >
        <SideSection onClick={(e) => e.stopPropagation()}>
          {phase === 'bid' ? (
            <BidPhase
              data={data}
              end={end}
              price={price}
              setPrice={setPrice}
              selected={selected}
              setSelected={setSelected}
              setPhase={setPhase}
            />
          ) : (
            <PayPhase data={data} end={end} price={price} />
          )}
        </SideSection>
      </Wrapper>
    )
  );
};

const Wrapper = styled.div`
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
`;

const SideSection = styled.div`
  width: 500px;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  position: absolute;
  right: 0px;
  z-index: 100;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  h2 {
    padding: 40px 20px 0 20px;
    font-size: 24px;
    font-weight: 500;
    align-self: flex-start;
    text-align: left;
    line-height: 1.5;
    color: #5a5a5a;
  }
`;

export default AucsSideSection;
