import AucItem from '@/components/AucItem';
import NorItem from '@/components/NorItem';
import React from 'react';
import styled from 'styled-components';
// TODO : 어떤 카테고리에 따라 보여줄지 정하기
type MainProps = {
  data: any;
  category: string;
  orderBy: string;
  setOrderBy: React.Dispatch<React.SetStateAction<string>>;
};
const Main = ({ data, category, orderBy, setOrderBy }: MainProps) => {
  const orders = ['최신순', '낮은가격순', '높은가격순'];
  return (
    <Container>
      <CategoryInfo>
        SHOP {'>'} 검색 {category ? ` > ${category}` : ''}
      </CategoryInfo>
      <ResultBar>
        <div>
          <h2>검색결과</h2>
          <p>{data.length}개 상품</p>
        </div>
        <Sort>
          {orders.map((order, index) => (
            <div key={index}>
              <p
                key={index}
                onClick={() => {
                  setOrderBy(order);
                }}
                style={
                  orderBy === order ? { color: '#000', fontWeight: '600' } : {}
                }
              >
                {order}
              </p>
              &nbsp; &nbsp;
              {index !== orders.length - 1 && <p>|</p>}
            </div>
          ))}
        </Sort>
      </ResultBar>
      <ItemList>
        {data.map((item: any) => {
          if (item.price !== null && item.price !== undefined) {
            return (
              <NorItem
                key={item.productsId}
                id={item.productsId}
                img={item.imageUrl}
                name={item.name}
                price={item.price}
              />
            );
          } else {
            return (
              <AucItem
                key={item.productsId}
                id={item.productsId}
                img={item.imageUrl}
                name={item.name}
                price={item.startPrice}
                end={item.end}
              />
            );
          }
        })}
      </ItemList>
    </Container>
  );
};

const Container = styled.div`
  width: 100%;
  height: 100%;
  padding: 2px 80px;
`;

const CategoryInfo = styled.div`
  width: 100%;
  height: 100%;
  padding: 0px;
  display: flex;
  font-size: 16px;
  justify-content: space-between;
  align-items: center;
  color: #a1a1a1;
`;

const ResultBar = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 40px;
  margin-bottom: 20px;
  div {
    display: flex;
    align-items: center;
    h2 {
      margin-right: 12px;
    }
    p {
      color: #6a6a6a;
    }
  }
`;

const Sort = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  p {
    font-size: 16px;
    color: #a1a1a1;
    cursor: pointer;
  }
  div + div {
    margin-left: 12px;
  }
`;

const ItemList = styled.div`
  min-width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 32px 12px;
  flex-wrap: wrap;
  margin-bottom: 40px;
`;

export default Main;
