import React from 'react';
import styled from 'styled-components';
import { ReactComponent as LineRow } from '@assets/line_row.svg';
import { ReactComponent as AucIcon } from '@assets/auc_icon.svg';
import { ReactComponent as NorIcon } from '@assets/nor_icon.svg';
import Data from '@/dummy.json';

const PurchaseHistory = () => {
  return (
    <Wrapper>
      <h4>구매 내역</h4>
      <Contents>
        <Header>
          <p style={{ margin: '0 160px' }}>주문내역</p>
          <p>주문일</p>
          <p style={{ marginLeft: '20px' }}>주문번호</p>
          <p>주문금액</p>
          <p>진행상태</p>
        </Header>
        <ContentList>
          {/* {Data.items.map((item) => (
            <>
              <ContentItem>
                <img src="https://github.com/MinJaeSon/assets/blob/main/auc_item.png?raw=true" />
                <ColDiv>
                  <AucIcon style={{ width: '28px' }} />
                  <p>Thisisneverthat x New Era x MLB New York Yankees New</p>
                  <span>54,300원</span>
                </ColDiv>
                <p>2024.02.07</p>
                <p>01390292</p>
                <p>54,300원</p>
                <span>응찰중</span>
              </ContentItem>
              <LineRow style={{ width: '100%' }} />
            </>
          ))} */}
        </ContentList>
      </Contents>
    </Wrapper>
  );
};

export default PurchaseHistory;

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 1000px;
  h4 {
    text-align: left;
  }
`;

const Contents = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const Header = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 32px;
  gap: 20px;
  width: 100%;
  height: 44px;
  border-top: 1px solid #8993a2;
  border-bottom: 1px solid #ebebeb;
  p {
    color: #222222;
    font-size: 0.9rem;
  }
`;

const ContentList = styled.div`
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 0 40px 0 24px;
  width: 100%;
`;

const ContentItem = styled.div`
  display: flex;
  flex-direction: row;
  gap: 20px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  img {
    width: 60px;
    height: 60px;
  }
  p {
    font-size: 0.8rem;
    color: #727272;
  }
  p:nth-child(2) {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 300px;
  }
  span {
    font-size: 0.8rem;
    font-weight: 600;
    color: #222222;
  }
`;

const ColDiv = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: start;
  gap: 8px;
  margin-left: -80px;
`;
