import React from 'react';
import styled from 'styled-components';
import { ReactComponent as LineRow } from '@assets/line_row.svg';
import { ReactComponent as AucIcon } from '@assets/auc_icon.svg';
import { ReactComponent as NorIcon } from '@assets/nor_icon.svg';
import Data from '@/dummy.json';

const Likes = () => {
  const token = localStorage.getItem('token');
  if (!token) {
    alert('로그인이 필요한 서비스입니다.');
    window.location.replace('/');
  }

  return (
    <Wrapper>
      <h4>관심</h4>
      <Contents>
        <Header>
          <p>전체 </p>
          <p>최신순</p>
        </Header>
        <ContentList>
          {/* {Data.items.map((item) => (
            <>
              <ContentItem>
                <img src="https://github.com/MinJaeSon/assets/blob/main/auc_item.png?raw=true" />
                <ColDiv>
                  <AucIcon style={{ width: '28px' }} />
                  <p>Thisisneverthat x New Era x MLB New York Yankees New</p>
                  <span>남은시간 18시 28분 32초</span>
                </ColDiv>
              </ContentItem>
              <LineRow style={{ width: '100%' }} />
            </>
          ))} */}
        </ContentList>
      </Contents>
    </Wrapper>
  );
};

export default Likes;

// const AucBtn = () => {
//   return (

//   )
// }

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 1000px;
  h4 {
    text-align: left;
  }
`;

const Contents = styled.div`
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const Header = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 32px;
  gap: 20px;
  width: 100%;
  height: 44px;
  border-top: 1px solid #8993a2;
  border-bottom: 1px solid #ebebeb;
  p {
    color: #222222;
    font-size: 0.9rem;
  }
`;

const ContentList = styled.div`
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 0 40px 0 24px;
  width: 100%;
`;

const ContentItem = styled.div`
  display: flex;
  flex-direction: row;
  gap: 20px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  img {
    width: 60px;
    height: 60px;
  }
  p {
    font-size: 0.8rem;
    color: #727272;
  }
  p:nth-child(2) {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 300px;
  }
  span {
    font-size: 0.8rem;
    font-weight: 600;
    color: #222222;
  }
`;

const ColDiv = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: start;
  gap: 6px;
  margin-left: -80px;
  p {
    color: #212121;
    font-size: 0.9rem;
    font-weight: 600;
  }
  span {
    color: #a1a1a1;
    font-size: 0.7rem;
    font-weight: 400;
  }
`;
