import Divider from '@/components/Divider';
import React, { useCallback, useState } from 'react';
import styled from 'styled-components';
import Layout from './Layout';
import useInput from '@/hooks/useInput';
import { LabelDiv, PriceDiv, SubmitButton, SubmitDiv } from './style';
import dayjs from 'dayjs';
import 'dayjs/locale/ko';
import api from '@/api/api';
import { useNavigate } from 'react-router-dom';
import * as shop from '@/pages/Shop/index';

//
//
//

interface ImageProps {
  image_file: string[] | null;
  preview_URL: string[] | null;
}

//
//
//

const AucSell = () => {
  dayjs.locale('ko');
  const [images, setImages] = useState<ImageProps | null>({
    image_file: [],
    preview_URL: [],
  });
  const [kind, setKind] = useState('' as string);
  const [name, onChangeName] = useInput('');
  const [information, onChangeInfo] = useInput('');
  const [tags, setTags] = useState([] as string[] | null);
  const [price, setPrice] = useState('');
  const [day, setDay] = useState(4);
  const [hour, setHour] = useState(0);
  const [minute, setMinute] = useState(0);
  const navigate = useNavigate();

  const formatWithComma = (inputValue: string) => {
    const numericValue = inputValue.replace(/,/g, ''); // Remove old commas
    return numericValue.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  };
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = e.target;
    const commaValue = formatWithComma(value);
    setPrice(commaValue);
  };

  const onSubmit = useCallback(async () => {
    const startPrice = price.replace(/,/g, ''); // 콤마 제거
    const tag = tags?.map((tag) => `${tag}`).join(' ');
    const end = dayjs()
      .add(day, 'day')
      .set('hour', hour)
      .set('minute', minute)
      .format('YYYY-MM-DDTHH:mm:00');

    const formData = new FormData();
    if (images?.image_file) {
      for (let i = 0; i < images.image_file.length; i++) {
        formData.append('images[' + i + ']', images.image_file[i]);
      }
    }
    formData.append('name', name);
    formData.append('information', information);
    formData.append('category', 'AUCS');
    formData.append('kind', kind);
    formData.append('startPrice', startPrice);
    if (tag) formData.append('tags', tag);
    formData.append('end', end);

    await api.post('/product/register', formData).then((res) => {
      console.log(res);
      if (res.status === 200) {
        alert('등록이 완료되었습니다.');
        navigate('/');
      }
      // navigate('/sell/aucs/complete?id=');
      // 추후 sell/aucs/complete?id 로 연결 필요
    });
  }, [images, kind, name, price, information, tags, day, hour, minute]);

  return (
    <Container>
      <shop.Title>SELL</shop.Title>
      <Divider />
      <Content>
        <Info> SELL &nbsp;{'>'}&nbsp; 경매판매 </Info>
        <Layout
          kind={kind}
          setKind={setKind}
          images={images}
          setImages={setImages}
          name={name}
          onChangeName={onChangeName}
          information={information}
          onChangeInfo={onChangeInfo}
          tags={tags}
          setTags={setTags}
        />
        <h2>경매설정</h2>
        <Divider backgroundColor="#000" marginBottom="26px" />
        <LabelDiv>
          <div>
            <span>시작가</span>
            <p>*</p>
          </div>
          <PriceDiv>
            <input
              type="text"
              value={price}
              onChange={(e) => {
                handleChange(e);
              }}
              placeholder="가격을 입력해주세요"
            />
            <p>원</p>
          </PriceDiv>
        </LabelDiv>
        <Divider marginTop="36px" marginBottom="36px" />
        <LabelDiv>
          <div>
            <span>경매마감</span>
            <p>*</p>
          </div>
          <Select
            defaultValue={day}
            onChange={(e) => {
              setDay(Number(e.target.value));
            }}
          >
            {[...Array(10)].map((_, index) => (
              <option key={index} value={index + 1}>
                {index + 1}
              </option>
            ))}
          </Select>
          &nbsp; 일 진행
          <Date>
            <p>{dayjs().add(day, 'day').format('YYYY년 MM월 DD일(ddd)')}</p>
          </Date>
          &nbsp;&nbsp; 일
          <Select
            style={{ marginLeft: '16px' }}
            onChange={(e) => {
              setHour(Number(e.target.value));
            }}
          >
            {[...Array(24)].map((_, index) => (
              <option key={index} value={index}>
                {index < 10 ? `0${index}` : index}
              </option>
            ))}
          </Select>
          &nbsp; 시
          <Select
            style={{ marginLeft: '16px' }}
            onChange={(e) => {
              setMinute(Number(e.target.value));
            }}
          >
            {[...Array(60)].map((_, index) => (
              <option key={index} value={index}>
                {index < 10 ? `0${index}` : index}
              </option>
            ))}
          </Select>
          &nbsp; 분까지
        </LabelDiv>
      </Content>
      <SubmitDiv>
        <SubmitButton color="#797B7C" backgroundColor="#fff">
          임시저장
        </SubmitButton>
        <SubmitButton color="#fff" backgroundColor="#FF6D6D" onClick={onSubmit}>
          등록하기
        </SubmitButton>
      </SubmitDiv>
    </Container>
  );
};

const Container = styled.div`
  width: 100%;
  height: 100%;
  padding: 0px;
`;

const Title = styled.div`
  font-size: 32px;
  font-weight: normal;
  margin-bottom: 20px;
`;

const Info = styled.div`
  width: 100%;
  font-size: 16px;
  font-weight: normal;
  margin-bottom: 40px;
  display: flex;
  align-self: start;
  color: #a1a1a1;
`;

const Content = styled.div`
  display: flex;
  flex-direction: column;
  align-items: start;
  padding: 40px 160px;
  h2 {
    color: #222;
    font-size: 20px;
    margin: 36px 0;
  }
`;

const Select = styled.select`
  width: 95px;
  height: 50px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #d1d1d1;
  outline: none;
  padding: 0px 24px;
  option {
    color: #555;
  }
`;

const Date = styled.div`
  width: 200px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  border: 1px solid #d1d1d1;
  background: #f8f8f8;
  margin-left: 40px;
  p {
    color: #555;
    font-size: 16px;
    margin: 0;
  }
`;

export default AucSell;
