import React from 'react';
import './App.css';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Main from '@pages/Main';
import Shop from '@pages/Shop';
import Sell from '@pages/Sell';
import Login from '@pages/Login';
import Header from '@components/Header';
import TopBar from './components/TopBar';
import MyPage from './pages/MyPage';
import Likes from './pages/Likes';
import { GoogleOAuthProvider } from '@react-oauth/google';
import SignUp from '@pages/SignUp';
import AucSell from './pages/Sell/AucSell';
import SaleSell from './pages/Sell/SaleSell';
import Complete from './pages/Sell/Complete';
import Detail from './pages/Detail';
import PurchaseHistory from '@pages/MyPage/PurchaseHistory';
import SalesHistory from './pages/MyPage/SalesHistory';
import ProfileManagement from './pages/MyPage/ProfileManagement';
import DeliveryManagement from './pages/MyPage/DeliveryManagement';
import Charging from './pages/MyPage/Charging';
import Quit from './pages/MyPage/Quit';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';

export const queryClient = new QueryClient();

function App() {
  const clientId =
    '618582969006-t9lvua47au1kj9lhbra9spguu31un4fc.apps.googleusercontent.com';
  return (
    <div className="App">
      <QueryClientProvider client={queryClient}>
        <GoogleOAuthProvider clientId={clientId}>
          <Router>
            <TopBar />
            <Header />
            <Routes>
              <Route path="/" element={<Main />} />
              <Route path="/shop" element={<Shop />} />
              <Route path="/sell" element={<Sell />} />
              <Route path="/sell/auc" element={<AucSell />} />
              <Route path="/sell/sale" element={<SaleSell />} />
              <Route path="/sell/:category/complete" element={<Complete />} />
              <Route path="/detail/:id" element={<Detail />} />
              <Route path="/mypage" element={<MyPage />}>
                <Route path="/mypage/purchase" element={<PurchaseHistory />} />
                <Route path="/mypage/sales" element={<SalesHistory />} />
                <Route path="/mypage/likes" element={<Likes />} />
                <Route
                  path="/mypage/myprofile"
                  element={<ProfileManagement />}
                />
                <Route
                  path="/mypage/delivery"
                  element={<DeliveryManagement />}
                />
                <Route path="/mypage/charge" element={<Charging />} />
                <Route path="/mypage/withdraw" element={<Quit />} />
              </Route>
              <Route path="/signup" element={<SignUp />} />
              <Route path="*" element={<div>Not Found</div>} />
            </Routes>
          </Router>
        </GoogleOAuthProvider>
      </QueryClientProvider>
    </div>
  );
}

export default App;
