import React, { useEffect } from 'react';
import { Outlet, NavLink, useLocation, useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import Default from './Default';

const MyPage = () => {
  const location = useLocation();
  const token = localStorage.getItem('token');

  if (!token) {    
    alert('로그인이 필요한 서비스입니다.');
    window.location.replace('/');
  }


  return (
    <Wrapper>
      <Sidebar>
        <h3 onClick={() => window.location.replace('/mypage')} style={{ cursor: 'pointer' }}>마이페이지</h3>
        <MenuSection>
          <h4>쇼핑 정보</h4>
          <MenuList>
            <MenuItem to="purchase">구매 내역</MenuItem>
            <MenuItem to="sales">판매 내역</MenuItem>
            <MenuItem to="likes">관심</MenuItem>
          </MenuList>
        </MenuSection>
        <MenuSection>
          <h4>내정보</h4>
          <MenuList>
            <MenuItem to="myprofile">프로필관리</MenuItem>
            <MenuItem to="delivery">배송지관리</MenuItem>
            <MenuItem to="charge">포인트충전</MenuItem>
          </MenuList>
        </MenuSection>
        <MenuSection>
          <h4>계정관리</h4>
          <MenuList>
            <MenuItem to="withdraw">탈퇴하기</MenuItem>
          </MenuList>
        </MenuSection>
      </Sidebar>
      <Contents>
        {location.pathname === '/mypage' ? <Default /> : <Outlet /> }
      </Contents>
    </Wrapper>
  );
};

export default MyPage;

const Wrapper = styled.div`
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  position: relative;
`;

const Sidebar = styled.div`
  width: 200px;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 40px;
  left: 100px;
  align-items: start;
  h3 {
    font-family: Noto Sans;
    font-size: 1.2rem;
    font-weight: 600;
    margin-bottom: 24px;
  }
`;

const MenuSection = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 32px;
  h4 {
    font-size: 1.1rem;
    text-align: left;
  }
`;

const MenuList = styled.div`
  display: flex;
  flex-direction: column;
  margin-top: 12px;
  align-items: start;
`;

const MenuItem = styled(NavLink)`
  font-family: Noto Sans;
  font-size: 0.9rem;
  font-weight: 400;
  color: #a6a7a6;
  text-decoration: none;
  margin-bottom: 8px;
  &:hover {
    cursor: pointer;
  }
  &.active {
    color: #222222;
    font-weight: 600;
  }
`;

const Contents = styled.div`
  padding: 40px 100px 120px 320px;
`;
